import React from "react";
import { NavLink } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  CContainer,
  CHeader,
  CHeaderNav,
  CHeaderToggler,
  CNavItem,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilMenu } from "@coreui/icons";
import Profile from "../assets/profile.jpg";
import { useNavigate } from "react-router";

const AppHeader = (props) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { details } = useSelector((state) => state);

  const logout = () => {
    localStorage.clear();
    navigate("/login");
  };
  return (
    <CHeader position="sticky" className="px-0">
      <CContainer fluid>
        <CHeaderToggler
          className="ps-1"
          onClick={() => dispatch({ type: "set" })}
        >
          <CIcon icon={cilMenu} size="lg" />
        </CHeaderToggler>

        <CHeaderNav className=" me-auto ">
          <CNavItem>
            <div
              style={{
                /* fontFamily: "'Poppins',sans-serif", */ color: "black",
                fontWeight: "rgb(49, 49, 49)",
              }}
            >
              {props.name.toUpperCase()}
            </div>
          </CNavItem>
        </CHeaderNav>

        <CHeaderNav>
          <div>
            <img
              src={details.filepath == "---" ? Profile : details.filepath}
              style={{
                width: "2rem",
                height: "1.9rem",
                borderRadius: "100px",
                margin: "auto 10px",
              }}
              alt="profile_picture"
            />
            <span
              style={{
                cursor: "context-menu",
                color: "black",
                fontWeight: "500",
              }}
            >
              {details.name.length > 10
                ? `${details.name.slice(0, 10)}...`
                : details.name}
            </span>

            <CDropdown>
              <CDropdownToggle
                color="white"
                className="text-dark"
                style={{
                  border: "0px",
                }}
              ></CDropdownToggle>
              <CDropdownMenu>
                <CDropdownItem
                  className="cursorpointer"
                  onClick={() =>
                    dispatch({
                      type: "myprofileViewModal",
                      payload: {
                        value: true,
                        id: localStorage.getItem("id"),
                      },
                    })
                  }
                  style={{ fontSize: "0.9rem" }}
                >
                  View
                </CDropdownItem>
                <CDropdownItem
                  className="cursorpointer"
                  onClick={() => {
                    logout();
                  }}
                  style={{ fontSize: "0.9rem" }}
                >
                  Log out
                </CDropdownItem>
              </CDropdownMenu>
            </CDropdown>
          </div>
        </CHeaderNav>
      </CContainer>
    </CHeader>
  );
};

export default AppHeader;
