import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
import "./generate.css";
import G from "./certificate.png";

const Certificate = () => {
  const obj = useParams();
  const { name, months } = obj;
  const [visible, setVisible] = useState(true);

  return (
    <>
      {visible ? (
        <>
          <div className="m-3">
            <div>
              Name : <span>{name}</span>
            </div>
            <div>
              Months : <span>{months}</span>
            </div>
          </div>

          <button
            onClick={() => {
              setVisible(false);
              setTimeout(() => {
                window.print();
              }, 1000);
            }}
            className="btn btn-sm btn-success p-1 px-3 text-white m-3"
            style={{}}
          >
            print
          </button>
        </>
      ) : (
        <div className="mainbox">
          <div className="main-page">
            <div className="sub-page">
              <img className="page" src={G} />
              <div>
                <div className="name">{name}</div>
                <div className="months">{months}</div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};
export default Certificate;
