import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CTableHead,
  CCardTitle,
  CCardSubtitle,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";
import { cilHome } from "@coreui/icons";
import "./style.css";

const Dashboard = () => {
  const adminType = localStorage.getItem("admintype");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [records, setRecords] = useState([]);
  const [notificationList, setNotificatinList] = useState([]);
  const [users, setUser] = useState([]);

  useEffect(() => {
    dashboardGET();

    usersGET();
  }, []);

  const dashboardGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.dashboardGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setRecords(result.data.records);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const usersGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.statusUserListGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setUser(result.data.Users);
      console.log(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const datefilter = (x) => {
    const current = new Date();
    const date = formatDate(x);
    const today = formatDate(current);
    const yesterday = formatDate(current.setDate(current.getDate() - 1));

    if (date == today) {
      return <b>Today</b>;
    } else if (date == yesterday) {
      return "Yesterday";
    } else {
      return date;
    }
  };

  return (
    <>
      <div
        style={{
          display: "flex",
          scrollSnapType: "x mandatory",
          scrollPadding: "25px",
          columnGap: "20px",
          justifyContent: "space-between",
          paddingBottom: "0.5rem",
          overflowX: "scroll",
          overflowY: "hidden",
          marginTop: "25px",
          marginBottom: "15px",
        }}
        className="dashboard-scroll mx-2"
      >
        {records.map((val, id) => {
          if (id == 1 && adminType == "admin2") {
            return null;
          }
          return (
            <CCard
              style={{
                width: "200px",
                height: "125px",
                borderRadius: "0px",
                borderLeft: "3px solid #58d8b1",
                flex: "0 0 200px",
                scrollSnapAlign: "start",
                padding: "5px",
              }}
              key={id}
            >
              <CCardBody>
                <CCardTitle className="h3 ">
                  <div
                    style={{
                      width: "100%",
                      display: "flex",
                      justifyContent: "space-between",
                      alignContent: "center",
                    }}
                  >
                    <b className="py-2">{val.count}</b>
                    <i className={`${val.logo} fa-sm mt-4`}></i>
                  </div>
                </CCardTitle>
                <CCardSubtitle
                  className="mb-2 "
                  style={{
                    fontFamily: "'Poppins',sans-serif",
                    fontWeight: "bold",
                  }}
                >
                  {val.title}
                </CCardSubtitle>
              </CCardBody>
            </CCard>
          );
        })}
      </div>

      <CRow>
        <CCol>
          <CCard style={{ margin: "5px" }}>
            <CCardBody
              style={{
                overflowY: "scroll",
                height: "25rem",
                padding: "15px",
              }}
            >
              <div
                style={{
                  position: "sticky",
                  top: "-15px",
                  paddingTop: "15px",
                  paddingBottom: "20px",
                  backgroundColor: "white",
                }}
              >
                <CRow>
                  <CCol>
                    <CCol className="d-flex pb-2 justify-content-between">
                      <h4
                        style={{
                          fontFamily: "'Poppins',sans-serif",
                          fontWeight: "bold",
                          marginLeft: "5px",
                        }}
                      >
                        Status
                      </h4>
                      <CButton
                        color="primary"
                        size="sm"
                        onClick={() => {
                          navigate("/status");
                        }}
                      >
                        See All
                      </CButton>
                    </CCol>
                  </CCol>
                </CRow>
              </div>

              {/* User Table */}
              <CTable>
                <CTableHead
                  style={{
                    position: "sticky",
                    top: "35px",
                    backgroundColor: "white",
                  }}
                >
                  <CTableRow>
                    <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col">last Update</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Description</CTableHeaderCell>
                    <CTableHeaderCell scope="col"></CTableHeaderCell>
                  </CTableRow>
                </CTableHead>

                <CTableBody>
                  {users.map((val, id) => {
                    return (
                      <CTableRow key={id}>
                        <CTableDataCell>
                          <img
                            src={val.filepath ? val.filepath : Profile}
                            style={{
                              width: "50px",
                              height: "50px",
                              borderRadius: "100px",
                            }}
                          />
                        </CTableDataCell>
                        <CTableDataCell className="pt-3">
                          {val.name}
                        </CTableDataCell>

                        <CTableHeaderCell>
                          {val.lastStatus ? datefilter(val.lastStatus) : "---"}
                        </CTableHeaderCell>
                        <CTableHeaderCell>
                          {val.lastDes ? val.lastDes : "---"}
                        </CTableHeaderCell>
                        <CTableDataCell>
                          <CButton
                            color="success"
                            size="sm"
                            onClick={() => navigate("/status/" + val._id)}
                          >
                            Status
                          </CButton>
                        </CTableDataCell>
                      </CTableRow>
                    );
                  })}
                </CTableBody>
              </CTable>
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                {users.length == 0 ? <div>No Records</div> : null}
              </div>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};
export default Dashboard;
