import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CTableHead,
  CCardTitle,
  CCardSubtitle,
  CHeaderDivider,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";

const Status = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);

  const [userArr, setUserArr] = useState([]);

  useEffect(() => {
    dispatch({ type: "loaderModal" });
    setTimeout(() => {
      getStatusList();
    }, 1000);
  }, []);

  const datefilter = (x) => {
    const current = new Date();
    const date = formatDate(x);
    const today = formatDate(current);
    const yesterday = formatDate(current.setDate(current.getDate() - 1));

    if (date == today) {
      return <b>Today</b>;
    } else if (date == yesterday) {
      return "Yesterday";
    } else {
      return date;
    }
  };

  const getStatusList = async () => {
    const result = await api.statusUserListGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setUserArr(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };
  return (
    <>
      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CHeaderDivider />
          <CTable>
            <CTableHead>
              <CTableRow style={{ whiteSpace: "nowrap" }}>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">last Update</CTableHeaderCell>
                <CTableHeaderCell scope="col">Description</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {userArr.map((val, id) => {
                return (
                  <CTableRow key={id} style={{ whiteSpace: "nowrap" }}>
                    <CTableHeaderCell scope="row">{id + 1}</CTableHeaderCell>
                    <CTableDataCell>
                      <img
                        src={val.filepath ? val.filepath : Profile}
                        style={{ width: "35px" }}
                      />
                    </CTableDataCell>
                    <CTableDataCell>{val.name}</CTableDataCell>
                    <CTableDataCell>
                      {val.lastStatus ? datefilter(val.lastStatus) : "---"}
                    </CTableDataCell>
                    <CTableDataCell>
                      {val.lastDes
                        ? val.lastDes.length >= 10
                          ? val.lastDes.slice(0, 10) + "..."
                          : val.lastDes
                        : "---"}
                    </CTableDataCell>
                    <CTableDataCell>
                      <CButton
                        color="success"
                        className="d-flex justify-content-center text-white align-items-center px-3"
                        style={{
                          fontSize: "0.75rem",
                        }}
                        size="sm"
                        onClick={() => {
                          navigate("/status/" + val._id);
                        }}
                      >
                        Status
                      </CButton>
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
        </CCardBody>
      </CCard>
    </>
  );
};
export default Status;
