import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CHeaderDivider,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CForm,
  CRow,
  CFormLabel,
  CFormTextarea,
} from "@coreui/react";
import { cilBadge } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import { cilSearch } from "@coreui/icons";
import Profile from "../../assets/profile.jpg";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";

const Status = () => {
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const { editStatus, viewProfileModal } = modal;

  const [description, setDescription] = useState("");
  const [statusArr, setStatusArr] = useState([]);
  const [totalstatus, setTotalstatus] = useState(0);
  const [todaysDate] = useState(formatDate(new Date()));
  const [limit, setLimit] = useState(25);
  const [disabled, setDisabled] = useState(false);
  const [disabled2, setDisabled2] = useState(false);

  useEffect(() => {
    setDisabled2(details.status == "active" ? false : true);
  }, [details.status]);

  useEffect(() => {
    getStatus();
  }, [limit, editStatus.value]);

  const addStatus = async () => {
    const body = { description };
    const result = await api.statusadd(body);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      toast.success(result.data.message, "success");
      setDescription("");
      getStatus();
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const getStatus = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.statusGET(limit);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setStatusArr(result.data.status);
      setTotalstatus(result.data.totalstatus);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const datefilter = (x) => {
    const current = new Date();
    const date = formatDate(x);
    const today = formatDate(current);
    const yesterday = formatDate(current.setDate(current.getDate() - 1));

    if (date == today) {
      return <b>Today</b>;
    } else if (date == yesterday) {
      return "Yesterday";
    } else {
      return date;
    }
  };

  return (
    <>
      <CCard style={{ marginTop: "10px" }}>
        <CCardBody>
          <CForm>
            <CCol>
              <CFormTextarea
                label="Description"
                value={description}
                onChange={(e) => {
                  setDescription(e.target.value);
                }}
                style={{
                  fontSize: "0.9rem",
                  minHeight: "100px",
                  fontWeight: "500",
                }}
                placeholder="Today's Status Text Here . . ."
              ></CFormTextarea>
            </CCol>

            <CCol className="d-flex align-items-end justify-content-end">
              <CButton
                /*  color="primary" */
                size="sm"
                className="d-flex justify-content-center text-white align-items-center px-3"
                style={{
                  fontSize: "0.75rem",
                  background: " #0c2d83",
                  color: "white",
                }}
                onClick={() => {
                  dispatch({ type: "loaderModal" });
                  setTimeout(() => {
                    addStatus();
                    getStatus();
                  }, 1000);
                }}
                className="mt-2"
                disabled={disabled2 || disabled}
              >
                Add Status
              </CButton>
            </CCol>
          </CForm>
        </CCardBody>
      </CCard>

      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CHeaderDivider />
          <CTable>
            <CTableHead>
              <CTableRow style={{ whiteSpace: "nowrap" }}>
                <CTableHeaderCell scope="col">Status No.</CTableHeaderCell>
                <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                <CTableHeaderCell scope="col">Description</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {statusArr.map((val, id) => {
                if (!disabled && todaysDate == formatDate(val.postDate)) {
                  setDisabled(true);
                }
                return (
                  <CTableRow key={id}>
                    <CTableHeaderCell scope="row">
                      {totalstatus - id}
                    </CTableHeaderCell>
                    <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                      {datefilter(val.postDate)}
                    </CTableDataCell>
                    <CTableDataCell
                      style={{
                        whiteSpace: "pre-wrap",
                        minWidth: "250px",
                      }}
                    >
                      {val.description}
                    </CTableDataCell>

                    <CTableDataCell>
                      {todaysDate == formatDate(val.postDate) ? (
                        <>
                          <CButton
                            color="info"
                            size="sm"
                            size="sm"
                            className="d-flex justify-content-center text-white align-items-center px-3"
                            style={{
                              fontSize: "0.75rem",
                            }}
                            onClick={() => {
                              dispatch({
                                type: "editstatus",
                                payload: {
                                  value: true,
                                  description: val.description,
                                  statusId: val._id,
                                },
                              });
                            }}
                          >
                            Edit
                          </CButton>
                        </>
                      ) : null}
                    </CTableDataCell>
                    <CTableDataCell>
                      {val.pending ? (
                        val.receiveId == localStorage.getItem("id") ? (
                          <i className="fa fa-circle pending"></i>
                        ) : null
                      ) : (
                        <i className="fa fa-check-double text-success" />
                      )}
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
            }}
          >
            {statusArr.length == totalstatus ? null : (
              <CButton
                color="secondary"
                variant="ghost"
                onClick={() => {
                  setLimit(limit + limit);
                }}
              >
                Load more
              </CButton>
            )}
            {totalstatus == 0 ? "No Records" : null}
          </div>
        </CCardBody>
      </CCard>
    </>
  );
};
export default Status;
