import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CForm,
  CFormSelect,
  CCol,
  CButton,
  CFormInput,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormTextarea,
} from "@coreui/react";
import api from "../../api";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router";

const addcertificateModal = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { addcertificateModal } = modal;

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [workIn, setWorkIn] = useState("");
  const [sdn, setSdn] = useState("");
  const [text, setText] = useState("");
  const [thumbnailName, setThumbnailName] = useState("");
  const [thumbnail, setThumbnail] = useState();
  const [status, setStatus] = useState("active");
  const [months, setMonths] = useState();

  useEffect(() => {
    setTimeout(() => {
      setName(addcertificateModal.obj.name);
      setEmail(addcertificateModal.obj.email);
      setWorkIn(addcertificateModal.obj.workIn);
      setSdn(addcertificateModal.obj.sdn);
      setMonths(addcertificateModal.obj.months);
    }, 500);
  }, [addcertificateModal.value]);

  const reset = () => {
    setName("");
    setEmail("");
    setWorkIn("");
    setSdn("");
    setText("");
    setThumbnailName("");
    setThumbnail();
    setMonths();
  };

  const close = () => {
    setThumbnailName("");
    setThumbnail();
    dispatch({ type: "addcertificateModal", payload: { value: false } });
  };

  const addCertificate = async () => {
    const userid = localStorage.getItem("id");
    const usertoken = localStorage.getItem("token");
    const usertype = localStorage.getItem("usertype");

    const body = {
      userid,
      usertype,
      usertoken,
      title: "Certificate",
      text,
      files: thumbnail,
      name,
      email,
      workIn,
      sdn,
      months,
    };

    dispatch({ type: "loaderModal" });
    const response = await api.addcertificate(body);
    dispatch({ type: "loaderModal" });

    if (response) {
      if (response.request.status == 200) {
        toast.success("Notification send successfully . . . 🎉");
        reset();
        close();
        dispatch({ type: "sendNotificationModal", payload: false });
      } else {
        if (response.response.data.err.message == "login session expire") {
          dispatch({ type: "addProfileModal", payload: false });
          localStorage.clear();
          toast.error("login session expire", "error");
          navigate("/login");
        } else {
          toast.error(response.response.data.err.message, "error");
        }
      }
    }
  };

  return (
    <>
      <CModal visible={addcertificateModal.value} onClose={() => close()}>
        <CModalHeader onClose={() => close()}>
          <CModalTitle>Certificate Details</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="row g-3">
            <CCol md={12}>
              <CFormInput
                type="name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                label="Name"
              />
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                label="Email"
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="text"
                value={workIn}
                onChange={(e) => {
                  setWorkIn(e.target.value);
                }}
                label="Work In (optional)"
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="text"
                value={sdn}
                onChange={(e) => {
                  setSdn(e.target.value);
                }}
                label="Senior Developer Name (optional)"
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="number"
                max={60}
                min={3}
                value={months}
                onChange={(e) => {
                  setMonths(e.target.value);
                }}
                label="Months"
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="title"
                value="Certificate"
                label="Title"
                disabled
              />
            </CCol>
            <CCol md={12}>
              <CFormSelect
                label="Status"
                className="p-1 px-2"
                value={status}
                onChange={(e) => {
                  setStatus(e.target.value);
                }}
              >
                <option value={"active"}>Active</option>
                <option value={"disabled"}>Disabled</option>
                <option value={"passout"}>Passout</option>
              </CFormSelect>
            </CCol>
            <CCol md={12}>
              <CFormTextarea
                label="Message (optional)"
                value={text}
                onChange={(e) => {
                  setText(e.target.value);
                }}
                rows="3"
              ></CFormTextarea>
            </CCol>

            <CCol md={8}>
              <CFormInput
                type="file"
                value={thumbnailName}
                multiple
                onChange={(e) => {
                  setThumbnail(e.target.files);
                  setThumbnailName(e.target.value);
                }}
                label="Files"
              />
            </CCol>
            {thumbnailName ? (
              <CCol md={1}>
                <i
                  className="fa fa-trash text-danger fa-2x py-1"
                  onClick={() => {
                    setThumbnail();
                    setThumbnailName("");
                  }}
                ></i>
              </CCol>
            ) : null}
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton color="success">
            <a
              href={`/clinginterns/certificate/generate/${name}/${months}`}
              target="_blank"
              style={{ textDecoration: "none", color: "white" }}
            >
              Generate
            </a>
          </CButton>
          <CButton
            color="secondary"
            onClick={() => {
              reset();
            }}
          >
            Reset
          </CButton>
          <CButton
            color="primary"
            onClick={() => {
              addCertificate();
            }}
          >
            Send Notification
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default addcertificateModal;
