import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CForm,
  CCol,
  CButton,
  CFormInput,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormTextarea,
} from "@coreui/react";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";

const editStatus = () => {
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { editStatus } = modal;

  const [statusId, setStatusId] = useState(null);
  const [description, setDescription] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setStatusId(editStatus.obj.statusId);
      setDescription(editStatus.obj.description);
    }, 500);
  }, [editStatus.value]);

  const statusedit = async () => {
    const body = { statusId, description };
    dispatch({ type: "loaderModal" });
    const result = await api.statusedit(body);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      toast.success(result.data.message, "success");
      dispatch({
        type: "editstatus",
        payload: { value: false },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };
  return (
    <>
      <CModal
        visible={editStatus.value}
        onClose={() =>
          dispatch({
            type: "editstatus",
            payload: { value: false },
          })
        }
      >
        <CModalHeader
          onClose={() =>
            dispatch({
              type: "editstatus",
              payload: { value: false },
            })
          }
        >
          <CModalTitle>Edit Status</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="row g-3">
            <CCol md={12}>
              <CFormTextarea
                label="Message"
                value={description}
                onChange={(e) => {
                  setDescription(e.target.value);
                }}
                rows="3"
              ></CFormTextarea>
            </CCol>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={() => statusedit()}>
            Save
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default editStatus;
