import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CForm,
  CRow,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilSearch } from "@coreui/icons";
import "rsuite/dist/rsuite.min.css";
import "./notification.css";
import Profile from "../../assets/profile.jpg";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";

const Notification = () => {
  const userid = localStorage.getItem("id");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { sendNotificationModal, viewNotificationModal, addcertificateModal } =
    modal;

  const usertype = localStorage.getItem("usertype");
  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(25);
  const [loading, setLoading] = useState(false);
  const [loadbtnShow, setLoadBtnShow] = useState(true);
  const [notificationList, setNotificatinList] = useState([]);

  useEffect(() => {
    notificationGET();
  }, [
    sendNotificationModal.value,
    viewNotificationModal.value,
    addcertificateModal.value,
    search,
    limit,
  ]);

  useEffect(() => {
    setTimeout(() => {
      setLimit(10);
    }, 50);
  }, [search]);

  const datefilter = (x) => {
    const current = new Date();
    const date = formatDate(x);
    const today = formatDate(current);
    const yesterday = formatDate(current.setDate(current.getDate() - 1));

    if (date == today) {
      return "Today";
    } else if (date == yesterday) {
      return "Yesterday";
    } else {
      return date;
    }
  };

  const notificationGET = async () => {
    setLoading(true);

    const result = await api.notificationGET(search, limit);

    if (result.request.status == 200) {
      setLoading(false);
      if (result.data.total < limit) {
        setLoadBtnShow(false);
      } else {
        setLoadBtnShow(true);
      }
      setNotificatinList(result.data.notifications);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const notification_modal_view = async (val) => {
    const userid = localStorage.getItem("id");
    const body = { textid: val._id };
  
    if (userid == val.receiveId) {
      const response = await api.notificationchecked(body);
    
      if (response) {
        if (response.request.status == 200) {
          dispatch({
            type: "viewNotificationModal",
            payload: { value: true, obj: val },
          });
          notificationGET();
        } else {
          if (response.response.data.err.message == "login session expire") {
            dispatch({ type: "addProfileModal", payload: false });
            localStorage.clear();
            toast.error("login session expire", "error");
            navigate("/login");
          } else {
            toast.error(response.response.data.err.message, "error");
          }
        }
      }
    } else {
      dispatch({
        type: "viewNotificationModal",
        payload: { value: true, obj: val },
      });
    }
  };

  const notificationdelete = async (textid) => {
    const userid = localStorage.getItem("id");
    const usertoken = localStorage.getItem("token");
    const usertype = localStorage.getItem("usertype");
    const body = { userid, usertype, usertoken, textid };
    if (usertype == "admin") {
      const response = await api.notificationdelete(body);

      if (response) {
        if (response.request.status == 200) {
          notificationGET();
        } else {
          if (response.response.data.err.message == "login session expire") {
            dispatch({ type: "addProfileModal", payload: false });
            localStorage.clear();
            toast.error("login session expire", "error");
            navigate("/login");
          } else {
            toast.error(response.response.data.err.message, "error");
          }
        }
      }
    }
  };

  const filterText = (x) => {
    const res = x.length >= 10 ? x.slice(0, 10) + "..." : x;
    return res;
  };

  const card = () => {
    return (
      <>
        {notificationList.map((val, id) => {
          const pending =
            val.pending && val.receiveId == localStorage.getItem("id");
          return (
            <CTableRow key={id} className={pending ? "pending" : ""}>
              <CTableHeaderCell
                scope="col"
                onClick={() => notification_modal_view(val)}
              >
                {id + 1}
              </CTableHeaderCell>

              <CTableDataCell onClick={() => notification_modal_view(val)}>
                <img
                  src={val.filepath ? val.filepath : Profile}
                  style={{
                    width: "2rem",
                    height: "1.9rem",
                    borderRadius: "100px",
                  }}
                />
              </CTableDataCell>
              <CTableDataCell
                onClick={() => notification_modal_view(val)}
                style={{ whiteSpace: "nowrap" }}
              >
                {val.sendId == userid ? (
                  <>
                    <b>To </b>
                    {val.receiveName}
                  </>
                ) : (
                  val.name
                )}
              </CTableDataCell>
              <CTableDataCell
                onClick={() => notification_modal_view(val)}
                style={{ whiteSpace: "nowrap" }}
              >
                {val.title ? filterText(val.title) : "---"}
              </CTableDataCell>
              <CTableDataCell
                onClick={() => notification_modal_view(val)}
                style={{ whiteSpace: "nowrap" }}
              >
                {val.text ? filterText(val.text) : "---"}
              </CTableDataCell>

              <CTableDataCell
                onClick={() => notification_modal_view(val)}
                style={{ whiteSpace: "nowrap" }}
              >
                {datefilter(val.date)}
              </CTableDataCell>

              <CTableDataCell
                style={{
                  paddingRight: "0px",
                }}
              >
                <i
                  className={"fa  fa-trash text-danger p-0 m-0"}
                  onClick={() => {
                    const a = confirm(`Do you want to Delete?`);
                    if (a) {
                      notificationdelete(val._id);
                    }
                  }}
                ></i>
              </CTableDataCell>

              <CTableDataCell onClick={() => notification_modal_view(val)}>
                {val.pending ? (
                  val.receiveId == localStorage.getItem("id") ? (
                    <i className="fa fa-circle pending"></i>
                  ) : null
                ) : (
                  <i className="fa fa-check-double text-success" />
                )}
              </CTableDataCell>
            </CTableRow>
          );
        })}
      </>
    );
  };
  return (
    <>
      <CCard style={{ marginTop: "10px" }}>
        <CCardBody>
          <CForm>
            <div className="d-flex">
              <CCol>
                <CInputGroup size="sm">
                  <CFormInput
                    type="search"
                    placeholder="search name . . ."
                    value={search}
                    onChange={(e) => {
                      setSearch(e.target.value);
                    }}
                  />
                  <CInputGroupText>
                    <CIcon icon={cilSearch}></CIcon>
                  </CInputGroupText>
                </CInputGroup>
              </CCol>
              <CCol md={8} className="d-flex p-0 justify-content-end px-2 ">
                <CButton
                  color="primary"
                  size="sm"
                  onClick={() =>
                    dispatch({
                      type: "sendNotificationModal",
                      payload: { value: true },
                    })
                  }
                  style={{
                    fontSize: "0.75rem",
                    background: " #0c2d83",
                    color: "white",
                  }}
                >
                  Send Notification
                </CButton>
              </CCol>
            </div>
          </CForm>
        </CCardBody>
      </CCard>

      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CTable hover small>
            <CTableHead>
              <CTableRow>
                <CTableHeaderCell scope="col">Sr</CTableHeaderCell>
                <CTableHeaderCell>Icon</CTableHeaderCell>
                <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Title</CTableHeaderCell>
                <CTableHeaderCell scope="col">Description</CTableHeaderCell>

                <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>{card()}</CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
            }}
          >
            {loadbtnShow ? (
              loading ? (
                <div className="spinner-border text-info"></div>
              ) : (
                <CButton
                  color="secondary"
                  variant="ghost"
                  onClick={() => {
                    setLimit(limit + limit);
                  }}
                >
                  Load more
                </CButton>
              )
            ) : null}

            {notificationList.length == 0 && loadbtnShow == false ? (
              <div>No Records</div>
            ) : null}
          </div>
        </CCardBody>
      </CCard>
    </>
  );
};
export default Notification;
