import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CTableHead,
  CCardTitle,
  CCardSubtitle,
  CHeaderDivider,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import Profile from "../../assets/profile.jpg";
import { useNavigate, useParams } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";
import { cilArrowCircleLeft } from "@coreui/icons";

const Status = () => {
  const paramsObj = useParams();
  const navigate = useNavigate();
  const { id } = useParams();
  const dispatch = useDispatch();
  const [statusArr, setStatusArr] = useState([]);
  const [limit, setLimit] = useState(25);
  const [totalstatus, setTotalstatus] = useState(0);

  useEffect(() => {
    statusCheck();
    dispatch({ type: "loaderModal" });
    setTimeout(() => {
      getStatus();
    }, 1000);
  }, []);

  const datefilter = (x) => {
    const current = new Date();
    const date = formatDate(x);
    const today = formatDate(current);
    const yesterday = formatDate(current.setDate(current.getDate() - 1));

    if (date == today) {
      return "Today";
    } else if (date == yesterday) {
      return "Yesterday";
    } else {
      return date;
    }
  };

  const getStatus = async () => {
    const result = await api.statusIdGET(limit, id);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setStatusArr(result.data.status);
      setTotalstatus(result.data.totalstatus);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const statusCheck = async () => {
    const { id } = paramsObj;
    const body = { cilentId: id };
    const result = await api.statusCheck(body);

    if (!result.request.status == 200) {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  return (
    <>
      <CCardBody className="my-3 ">
        <CRow>
          <CCol md={5}>
            <CIcon
              icon={cilArrowCircleLeft}
              size="xl"
              className="mx-1"
              onClick={() => {
                navigate("/status");
              }}
            />
          </CCol>
        </CRow>
      </CCardBody>

      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CHeaderDivider />
          <CTable>
            <CTableHead>
              <CTableRow style={{ whiteSpace: "nowrap" }}>
                <CTableHeaderCell scope="col">Status No</CTableHeaderCell>
                <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                <CTableHeaderCell scope="col">Description</CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {statusArr.map((val, id) => {
                return (
                  <CTableRow key={id}>
                    <CTableHeaderCell scope="row">
                      {totalstatus - id}
                    </CTableHeaderCell>
                    <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                      {datefilter(val.postDate)}
                    </CTableDataCell>
                    <CTableDataCell
                      style={{
                        whiteSpace: "pre-wrap",
                        minWidth: "250px",
                      }}
                    >
                      {val.description}
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
            }}
          >
            {statusArr.length == totalstatus ? null : (
              <CButton
                color="secondary"
                variant="ghost"
                onClick={() => {
                  setLimit(limit + limit);
                }}
              >
                Load more
              </CButton>
            )}

            {totalstatus == 0 ? "No Records" : null}
          </div>
        </CCardBody>
      </CCard>
    </>
  );
};
export default Status;
