import { createStore } from "redux";
import formatDate from "./methods/formatDate";

const initialState = {
  sidebarShow: true,
};

const modal = {
  myProfileModal: false,
  addProfileModal: false,
  viewProfileModal: {
    value: false,
    obj: {
      id: "",
      name: "---",
      email: "---",
      mobile: "---",
      dob: "---",
      gender: "---",
      state: "---",
      district: "---",
      course: "---",
      year: "---",
      github: "---",
      linkedin: "---",
      joinDate: "---",
      duration: "---",
      status: "---",
      filepath: null,
      endDate: "---",
      sdn: "---",
      workIn: "---",
    },
  },
  sendNotificationModal: {
    value: false,
    email: "",
  },
  viewNotificationModal: {
    value: false,
    obj: {
      id: "---",
      sendId: "",
      receiveId: "---",
      title: "---",
      text: "---",
      date: "---",
      name: "---",
      email: "---",
      files: [],
      notificationType: false,
      workIn: "",
      sdn: "",
      filepath: null,
      receiveName: "",
      months: 3,
    },
  },
  editProfileModal: false,
  myprofileViewModal: false,
  addcertificateModal: {
    value: false,
    obj: { name: "", email: "", sdn: "", workIn: "", months: 3 },
  },
  loaderModal: false,
  editStatus: {
    value: false,
    obj: { statusId: null, description: "" },
  },
};

const details = {
  name: "---",
  email: "---",
  mobile: "---",
  dob: "---",
  gender: "---",
  state: "---",
  district: "---",
  course: "---",
  year: "---",
  github: "---",
  linkedin: "---",
  joinDate: "---",
  duration: "---",
  status: "---",
  filepath: "---",
};

const changeState = (
  state = { details, initialState, modal },
  { type, payload }
) => {
  switch (type) {
    case "set":
      initialState.sidebarShow = !initialState.sidebarShow;
      return { ...state };
    case "addProfileModal":
      modal.addProfileModal = payload;
      return { ...state };
    case "viewProfileModal":
      modal.viewProfileModal.value = payload.value;
      const obj = modal.viewProfileModal.obj;
      if (payload.obj) {
        obj.id = payload.obj._id;
        obj.name = payload.obj.name;
        obj.email = payload.obj.email;
        obj.duration = payload.obj.duration;
        obj.filepath = payload.obj.filepath ? payload.obj.filepath : null;
        obj.joinDate = formatDate(payload.obj.joinDate);
        obj.status = payload.obj.status ? payload.obj.status : "current";
        obj.mobile = payload.obj.mobile ? payload.obj.mobile : "---";
        obj.gender = payload.obj.gender ? payload.obj.gender : "---";
        obj.state = payload.obj.state ? payload.obj.state : "---";
        obj.district = payload.obj.district ? payload.obj.district : "---";
        obj.course = payload.obj.course ? payload.obj.course : "---";
        obj.year = payload.obj.year ? payload.obj.year : "---";
        obj.dob = payload.obj.dob ? formatDate(payload.dob) : "---";
        obj.github = payload.obj.github ? payload.obj.github : "---";
        obj.linkedin = payload.obj.linkedin ? payload.obj.linkedin : "---";
        obj.endDate = payload.obj.endDate
          ? formatDate(payload.obj.endDate)
          : "---";
        obj.workIn = payload.obj.workIn ? payload.obj.workIn : "---";
        obj.sdn = payload.obj.sdn ? payload.obj.sdn : "---";
      }

      return { ...state };
    case "sendNotificationModal":
      modal.sendNotificationModal.value = payload.value;
      payload.email
        ? (modal.sendNotificationModal.email = payload.email)
        : (modal.sendNotificationModal.email = "");

      return { ...state };
    case "viewNotificationModal":
      const obj2 = modal.viewNotificationModal.obj;
      modal.viewNotificationModal.value = payload.value;
      if (payload.obj) {
        obj2.id = payload.obj._id;
        obj2.name = payload.obj.name;
        obj2.text = payload.obj.text;
        obj2.email = payload.obj.email;
        obj2.sendId = payload.obj.sendId;
        obj2.receiveName = payload.obj.receiveName
          ? payload.obj.receiveName
          : "";
        obj2.workIn = payload.obj.workIn ? payload.obj.workIn : "";
        obj2.sdn = payload.obj.sdn ? payload.obj.sdn : "";
        obj2.date = formatDate(payload.obj.date);
        obj2.notificationType = payload.obj.notificationType
          ? payload.obj.notificationType
          : false;
        obj2.title = payload.obj.title ? payload.obj.title : "---";
        obj2.files = payload.obj.files ? payload.obj.files : [];
        obj2.filepath = payload.obj.filepath ? payload.obj.filepath : null;
        obj2.months = payload.obj.months ? payload.obj.months : 3;
      }

      return { ...state };
    case "editProfileModal":
      modal.editProfileModal = payload;
      return { ...state };
    case "myprofileViewModal":
      modal.myprofileViewModal = payload;
      return { ...state };
    case "details":
      details.name = payload.name;
      details.email = payload.email;
      details.duration = payload.duration;
      details.joinDate = formatDate(payload.joinDate);
      details.status = payload.status ? payload.status : "current";
      details.mobile = payload.mobile ? payload.mobile : "---";
      details.gender = payload.gender ? payload.gender : "---";
      details.state = payload.state ? payload.state : "---";
      details.district = payload.district ? payload.district : "---";
      details.course = payload.course ? payload.course : "---";
      details.year = payload.year ? payload.year : "---";
      details.dob = payload.dob ? formatDate(payload.dob) : "---";
      details.github = payload.github ? payload.github : "---";
      details.linkedin = payload.linkedin ? payload.linkedin : "---";
      details.filepath = payload.filepath ? payload.filepath : "---";
      return { ...state };
    case "addcertificateModal":
      modal.addcertificateModal.value = payload.value;
      const obj3 = modal.addcertificateModal.obj;
      if (payload.obj) {
        obj3.email = payload.obj.email;
        obj3.name = payload.obj.name;
        obj3.sdn = payload.obj.sdn;
        obj3.workIn = payload.obj.workIn;
        obj3.months = payload.obj.months;
      } else {
        obj3.email = "";
        obj3.name = "";
        obj3.sdn = "";
        obj3.workIn = "";
        obj3.months = 3;
      }
      return { ...state };
    case "loaderModal":
      modal.loaderModal = !modal.loaderModal;
      return { ...state };
    case "editstatus":
      modal.editStatus.value = payload.value;
      if (payload.value) {
        modal.editStatus.obj.statusId = payload.statusId;
        modal.editStatus.obj.description = payload.description;
      }
      return { ...state };
    default:
      return state;
  }
};

const store = createStore(changeState);
export default store;
