import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CTableHead,
} from "@coreui/react";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";

const Dashboard = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [notificationList, setNotificatinList] = useState([]);

  useEffect(() => {
    notificationGET();
  }, []);

  const notificationGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.notificationGET(null, 10);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setNotificatinList(result.data.notifications);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const notification_modal_view = async (val) => {
    const userid = localStorage.getItem("id");

    const body = { userid, id: val._id };
    if (userid == val.receiveId) {
      dispatch({ type: "loaderModal" });
      const response = await api.notificationchecked(body);
      dispatch({ type: "loaderModal" });

      if (response) {
        if (response.request.status == 200) {
          dispatch({
            type: "viewNotificationModal",
            payload: { value: true, obj: val },
          });
          notificationGET();
        } else {
          if (response.response.data.err.message == "login session expire") {
            dispatch({ type: "addProfileModal", payload: false });
            localStorage.clear();
            toast.error("login session expire", "error");
            navigate("/login");
          } else {
            toast.error(response.response.data.err.message, "error");
          }
        }
      }
    } else {
      dispatch({
        type: "viewNotificationModal",
        payload: { value: true, obj: val },
      });
    }
  };
  return (
    <>
      <CButton
        color="success"
        size="sm"
        className="mt-4 text-white"
        onClick={() => navigate("/status")}
      >
        Go to Status Page
      </CButton>

      <CRow>
        <CCol md={12}>
          <CCard style={{ marginTop: "2%" }}>
            <CCardBody
              style={{
                overflowY: "scroll",
                height: "34rem",
                padding: "15px",
              }}
            >
              <div
                style={{
                  position: "sticky",
                  top: "-15px",
                  paddingTop: "15px",
                  paddingBottom: "20px",
                  backgroundColor: "white",
                }}
              >
                <CRow>
                  <CCol className="d-flex pb-2 justify-content-between">
                    <h4
                      style={{
                        fontFamily: "'Poppins',sans-serif",
                        fontWeight: "bold",
                        marginLeft: "5px",
                      }}
                    >
                      Notifications
                    </h4>
                    <CButton
                      color="primary"
                      size="sm"
                      onClick={() => {
                        navigate("/notification");
                      }}
                    >
                      See All
                    </CButton>
                  </CCol>
                </CRow>
              </div>

              <CTable>
                <CTableHead
                  style={{
                    position: "sticky",
                    top: "35px",
                    backgroundColor: "white",
                  }}
                >
                  <CTableRow>
                    <CTableHeaderCell scope="col">No.</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Title</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Description</CTableHeaderCell>

                    <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                    <CTableHeaderCell scope="col"></CTableHeaderCell>
                  </CTableRow>
                </CTableHead>
                <CTableBody>
                  {notificationList.map((val, id) => {
                    const pending =
                      val.pending &&
                      val.receiveId == localStorage.getItem("id");
                    return (
                      <CTableRow
                        key={id}
                        onClick={() => {
                          notification_modal_view(val);
                        }}
                        className={pending ? "text-info" : ""}
                      >
                        <CTableDataCell>{id + 1}</CTableDataCell>
                        <CTableDataCell>
                          {pending ? <b>{val.name}</b> : val.name}
                        </CTableDataCell>

                        <CTableDataCell>
                          {pending ? (
                            <b>{val.title ? val.title : "---"}</b>
                          ) : val.title ? (
                            val.title
                          ) : (
                            "---"
                          )}
                        </CTableDataCell>
                        <CTableDataCell>
                          {pending ? <b>{val.text}</b> : val.text}{" "}
                        </CTableDataCell>
                        <CTableDataCell>
                          {pending ? (
                            <b>{formatDate(val.date)}</b>
                          ) : (
                            formatDate(val.date)
                          )}
                        </CTableDataCell>
                        <CTableDataCell>
                          <i
                            className={pending ? "fa  fa-circle pending" : ""}
                          ></i>
                        </CTableDataCell>
                      </CTableRow>
                    );
                  })}
                </CTableBody>
              </CTable>
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                {notificationList.length == 0 ? <div>No Records</div> : null}
              </div>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};
export default Dashboard;
