import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import "./scss/style.scss";
import Login from "./screen/pages/Login";
import { AppLayout } from "./container";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import modals from "./screen/modals/index";
import Generate from "./screen/certificate/generate";

function App() {
  return (
    <>
      <BrowserRouter basename="/clinginterns/">
        {modals.loader()}
        <ToastContainer position="top-left" pauseOnHover={false} />

        <Routes>
          <Route
            path="/certificate/generate/:name/:months"
            element={<Generate />}
          />
          <Route path="/login" element={<Login />} />
          <Route path="*" element={<AppLayout />} />
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
