import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CHeaderDivider,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CForm,
  CRow,
  CFormSelect,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilSearch } from "@coreui/icons";
import Profile from "../../assets/profile.jpg";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";

const Certificate = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { addcertificateModal } = modal;

  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(10);
  const [loading, setLoading] = useState(false);
  const [loadbtnShow, setLoadBtnShow] = useState(true);
  const [certificateList, setCertificateList] = useState([]);

  useEffect(() => {
    certificateGET();
  }, [addcertificateModal.value, search, limit]);

  const certificateGET = async () => {
    setLoading(true);
    const result = await api.certificateGET(search, limit);

    if (result.request.status == 200) {
      setLoading(false);
      if (result.data.total < limit) {
        setLoadBtnShow(false);
      } else {
        setLoadBtnShow(true);
      }

      setCertificateList(result.data.certificateList);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const profile = async (id) => {
    dispatch({ type: "loaderModal" });
    const result = await api.profileGET(id);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      dispatch({
        type: "viewProfileModal",
        payload: { value: true, obj: result.data.details },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  return (
    <div>
      <CCard style={{ marginTop: "10px" }}>
        <CCardBody>
          <CForm>
            <CRow>
              <CCol md={5}>
                <CInputGroup>
                  <CFormInput
                    type="search"
                    placeholder="search name . . ."
                    value={search}
                    onChange={(e) => {
                      setSearch(e.target.value);
                    }}
                  />
                  <CInputGroupText>
                    <CIcon icon={cilSearch}></CIcon>
                  </CInputGroupText>
                </CInputGroup>
              </CCol>
              <CCol className="d-flex pb-2 justify-content-end">
                <CButton
                  color="primary"
                  size="sm"
                  onClick={() =>
                    dispatch({
                      type: "addcertificateModal",
                      payload: { value: true },
                    })
                  }
                >
                  Add Certificate
                </CButton>
              </CCol>
            </CRow>
          </CForm>
        </CCardBody>
      </CCard>

      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CHeaderDivider />
          <CTable>
            <CTableHead>
              <CTableRow>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                <CTableHeaderCell scope="col">Mobile</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {certificateList.map((val, id) => {
                return (
                  <CTableRow key={id}>
                    <CTableHeaderCell scope="row">{id + 1}</CTableHeaderCell>
                    <CTableDataCell>
                      <img
                        src={val.filepath ? val.filepath : Profile}
                        style={{ width: "50px" }}
                      />
                    </CTableDataCell>
                    <CTableDataCell>{val.name}</CTableDataCell>
                    <CTableDataCell>{val.email}</CTableDataCell>
                    <CTableDataCell>
                      {val.mobile ? val.mobile : "---"}
                    </CTableDataCell>
                    <CTableDataCell>
                      <CButton
                        color="info"
                        size="sm"
                        onClick={() => profile(val._id)}
                      >
                        View Profile
                      </CButton>
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
            }}
          >
            {loadbtnShow ? (
              loading ? (
                <div className="spinner-border text-info"></div>
              ) : (
                <CButton
                  color="secondary"
                  variant="ghost"
                  onClick={() => {
                    setLimit(limit + 10);
                  }}
                >
                  Load more
                </CButton>
              )
            ) : null}

            {certificateList.length == 0 && loadbtnShow == false ? (
              <div>No Records</div>
            ) : null}
          </div>
        </CCardBody>
      </CCard>
    </div>
  );
};
export default Certificate;
