import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CFormLabel,
  CFormCheck,
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CRow,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CInputGroupText,
  CInputGroup,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilLockLocked, cilUser } from "@coreui/icons";
import api from "../../api";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router";
import Bg from "./../../assets/bglow.jpg";
import Logo from "./../../assets/logo.png";
import "./login.css";

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [visible, setVisible] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [email2, setEmail2] = useState("");
  const [otp, setOtp] = useState("");
  const [npass, setNPass] = useState("");
  const [cpass, setCPass] = useState("");
  const [disabled, setDisabled] = useState(true);

  useEffect(() => {
    if (localStorage.getItem("token")) {
      navigate("/dashboard");
    }
  }, []);

  const login = async () => {
    dispatch({ type: "loaderModal" });
    const body = { email, password };
    const response = await api.login(body);
    dispatch({ type: "loaderModal" });

    if (response.request.status != 200) {
      toast.error(response.response.data.err.message, "error");
    } else {
      localStorage.clear();
      localStorage.setItem("token", response.data.token);
      localStorage.setItem("id", response.data.id);
      localStorage.setItem("usertype", response.data.usertype);
      if (response.data.usertype == "admin") {
        localStorage.setItem("admintype", response.data.admintype);
      }
      navigate("/dashboard");
    }
  };

  const forget = async () => {
    const body = { email: email2 };
    dispatch({ type: "loaderModal" });
    const response = await api.forget(body);
    dispatch({ type: "loaderModal" });

    if (response.request.status == 200) {
      setDisabled(false);
      toast.success(response.data.message, "success");
    } else {
      toast.error(response.response.data.err.message, "error");
    }
  };

  const change = async () => {
    const body = { email: email2, otp, cpass, npass };
    dispatch({ type: "loaderModal" });
    const response = await api.change(body);
    dispatch({ type: "loaderModal" });

    if (response.request.status == 200) {
      toast.success(response.data.message, "success");
      forgetClick();
      setVisible(false);
    } else {
      toast.error(response.response.data.err.message, "error");
    }
  };

  const modal = () => {
    return (
      <>
        <CModal visible={visible} onClose={() => setVisible(false)}>
          <CModalHeader onClose={() => setVisible(false)}>
            <CModalTitle className="forgot-title">Forget Password</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CForm className="row g-3">
              <CCol md={12}>
                <CFormInput
                  type="email"
                  placeholder="Email "
                  value={email2}
                  onChange={(e) => {
                    setEmail2(e.target.value);
                  }}
                  disabled={!disabled}
                />
              </CCol>

              <div className="d-flex">
                <CCol>
                  <CFormInput
                    type="otp"
                    label="Enter OTP"
                    value={otp}
                    onChange={(e) => {
                      setOtp(e.target.value);
                    }}
                    disabled={disabled}
                  />
                </CCol>

                <CCol md={3}>
                  <CButton
                    color="success"
                    style={{
                      marginLeft: "10px",
                      marginTop: "27px",
                      color: "white",
                    }}
                    onClick={() => {
                      forget();
                    }}
                    disabled={!disabled}
                  >
                    Send OTP
                  </CButton>
                </CCol>
              </div>

              <CCol md={6}>
                <CFormInput
                  type="password1"
                  label="New Password"
                  value={npass}
                  onChange={(e) => {
                    setNPass(e.target.value);
                  }}
                />
              </CCol>
              <CCol md={6}>
                <CFormInput
                  type="password2"
                  label="Confirm Password"
                  value={cpass}
                  onChange={(e) => {
                    setCPass(e.target.value);
                  }}
                />
              </CCol>
            </CForm>
          </CModalBody>

          <CModalFooter>
            <CButton
              style={{ backgroundColor: "#0c2d83" }}
              onClick={() => {
                change();
              }}
              disabled={disabled}
            >
              Save
            </CButton>
          </CModalFooter>
        </CModal>
      </>
    );
  };

  const forgetClick = () => {
    setEmail2("");
    setPassword("");
    setOtp("");
    setNPass("");
    setCPass("");
    setDisabled(true);
    setVisible(true);
  };
  return (
    <>
      {modal()}
      <div
        className="min-vh-100"
        style={{
          backgroundImage: `url(${Bg}) `,
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
          paddingBottom: "100px",
        }}
      >
        <div>
          <img src={Logo} alt="logo" className="login-logo" />
        </div>
        <CContainer>
          <CRow className="justify-content-center mt-4">
            <CCol md={6} className="login-slogan  ">
              <div className="login-animate">
                <div>
                  <span>Making </span>
                  <span className="login-green">y</span>
                  <span className="login-green login-underline">our ideas</span>
                </div>
                <div className=" d-flex justify-content-center">happen!</div>
              </div>
            </CCol>
            <CCol md={5}>
              <CCardGroup style={{ marginTop: "30px" }}>
                <CCard className="p-3 pt-1">
                  <CCardBody>
                    <CForm>
                      <div className="login-login mb-2">LOGIN FORM</div>

                      <CFormLabel className="login-label mt-4">
                        Email
                      </CFormLabel>
                      <CFormInput
                        placeholder="Email"
                        autoComplete="email"
                        value={email}
                        onChange={(e) => {
                          setEmail(e.target.value);
                        }}
                        className="login-input"
                      />
                      <CFormLabel
                        className="login-label login-input mt-4"
                        autoComplete="passwords"
                      >
                        Password
                      </CFormLabel>
                      {/*   <CInputGroup className="mb-4">
                        <CFormInput
                          type="password"
                          placeholder="Password"
                          autoComplete="current-password"
                          value={password}
                          onChange={(e) => {
                            setPassword(e.target.value);
                          }}
                        />
                        <CInputGroupText>
                          <CIcon icon={cilEyeem} />
                        </CInputGroupText>
                      </CInputGroup> */}
                      <CFormInput
                        type="password"
                        placeholder="Password"
                        autoComplete="current-password"
                        value={password}
                        onChange={(e) => {
                          setPassword(e.target.value);
                        }}
                      />
                      <CCol className="d-flex justify-content-between align-items-center mt-4">
                        <div>
                          <CFormLabel color="link" className="px-0 d-flex">
                            <input type="checkbox" className="login-checkbox" />
                            <small style={{ marginLeft: "5px" }}>
                              Remember me
                            </small>
                          </CFormLabel>
                        </div>
                        <CFormLabel
                          color="link"
                          className="px-0 login-forgetBtn"
                          style={{ textDecoration: "none" }}
                          onClick={() => forgetClick()}
                        >
                          <small> Forgot Password?</small>
                        </CFormLabel>
                      </CCol>
                      <CRow className="mt-2">
                        <CButton
                          /* color="#ed1f24" */
                          color="transparent"
                          className="px-4 login-loginBtn"
                          onClick={() => login()}
                        >
                          Login
                        </CButton>
                      </CRow>
                    </CForm>
                  </CCardBody>
                </CCard>
              </CCardGroup>
            </CCol>
          </CRow>
        </CContainer>
      </div>
    </>
  );
};

export default Login;
