import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CFormLabel,
  CButton,
  CFormInput,
  CFormTextarea,
  CForm,
  CRow,
  CFormCheck,
  CAlert,
} from "@coreui/react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";

const Certificate = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const [alertValue, setAlertValue] = useState(false);

  const [name, setName] = useState("");
  const [workIn, setWorkIn] = useState("");
  const [sdn, setSdn] = useState("");
  const [text, setText] = useState("");
  const [check, setCheck] = useState(false);
  const [months, setMonths] = useState(3);

  useEffect(() => {
    setTimeout(() => {
      if (details.status == "passout") {
        setAlertValue(true);
      }
    }, 100);
  }, []);

  const sendNotification = async () => {
    const userid = localStorage.getItem("id");
    const usertoken = localStorage.getItem("token");
    const usertype = localStorage.getItem("usertype");
    const body = {
      userid,
      usertype,
      usertoken,
      name,
      workIn,
      sdn,
      text,
      check,
      months,
    };
    dispatch({ type: "loaderModal" });
    const response = await api.applycertificate(body);
    dispatch({ type: "loaderModal" });

    if (response) {
      if (response.request.status == 200) {
        toast.success(response.data.message, "success");
        setName("");
        setWorkIn("");
        setSdn("");
        setText("");
        setMonths(3);
        setCheck(false);
      } else {
        if (response.response.data.err.message == "login session expire") {
          dispatch({ type: "addProfileModal", payload: false });
          localStorage.clear();
          toast.error("login session expire", "error");
          navigate("/login");
        } else {
          toast.error(response.response.data.err.message, "error");
        }
      }
    }
  };

  return (
    <div>
      {alertValue ? (
        <CAlert color="success" className="my-2">
          Your certificate already generated
        </CAlert>
      ) : null}

      <CCard
        style={{
          marginTop: "10px",
        }}
      >
        <CCardBody>
          {}
          <CForm>
            <CRow className="mb-3">
              <CFormLabel className="col-sm-2 col-form-label">
                Name :
              </CFormLabel>
              <CCol sm={5}>
                <CFormInput
                  type="name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </CCol>
            </CRow>
            <CRow className="mb-3">
              <CFormLabel className="col-sm-2 col-form-label">
                Work with :
              </CFormLabel>
              <CCol sm={5}>
                <CFormInput
                  type="text"
                  value={workIn}
                  onChange={(e) => setWorkIn(e.target.value)}
                />
              </CCol>
            </CRow>

            <CRow className="mb-3">
              <CFormLabel className="col-sm-2 col-form-label">
                Senior Developer Name :
              </CFormLabel>
              <CCol sm={5}>
                <CFormInput
                  type="text"
                  value={sdn}
                  onChange={(e) => setSdn(e.target.value)}
                />
              </CCol>
            </CRow>

            <CRow className="mb-3">
              <CFormLabel className="col-sm-2 col-form-label">
                Months :
              </CFormLabel>
              <CCol sm={5}>
                <CFormInput
                  type="number"
                  max={60}
                  min={3}
                  value={months}
                  onChange={(e) => {
                    setMonths(e.target.value);
                  }}
                />
              </CCol>
            </CRow>

            <CCol md={12}></CCol>

            <CRow className="mb-3">
              <CFormLabel className="col-sm-2 col-form-label">
                Message (optional):
              </CFormLabel>
              <CCol sm={5}>
                <CFormTextarea
                  value={text}
                  onChange={(e) => {
                    setText(e.target.value);
                  }}
                ></CFormTextarea>
              </CCol>
            </CRow>

            <fieldset className="row mb-3">
              <legend className="col-form-label col-sm-2 pt-0"></legend>
              <CCol sm={5}>
                <CFormCheck
                  value={check}
                  onChange={(e) => setCheck(!check)}
                  type="checkbox"
                  id="gridCheck"
                  label="I have verified the above information is accurate and I Understand that any incorrect
                  information delay the process of certificate request."
                />
              </CCol>
            </fieldset>

            <CButton
              onClick={() => sendNotification()}
              disabled={details.status == "active" ? false : true}
            >
              Send Notification
            </CButton>
          </CForm>
        </CCardBody>
      </CCard>
    </div>
  );
};
export default Certificate;
