import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CHeaderDivider,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CForm,
  CRow,
} from "@coreui/react";
import { cilBadge } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import { cilSearch, cilUser, cilTrash } from "@coreui/icons";
import Profile from "../../assets/profile.jpg";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";

const Users = () => {
  const adminType = localStorage.getItem("admintype");
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const { addProfileModal, addcertificateModal, viewProfileModal } = modal;
  const navigate = useNavigate();

  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(25);
  const [loading, setLoading] = useState(false);
  const [loadbtnShow, setLoadBtnShow] = useState(true);
  const [users, setUser] = useState([]);
  const [disabled, setDisabled] = useState(true);

  useEffect(() => {
    setDisabled(details.status == "active" ? false : true);
  }, [details.status]);

  useEffect(() => {
    usersGET();
  }, [
    addProfileModal,
    search,
    limit,
    addcertificateModal.value,
    viewProfileModal.value,
  ]);

  useEffect(() => {
    setTimeout(() => {
      setLimit(10);
    }, 50);
  }, [search]);

  const usersGET = async () => {
    setLoading(true);
    const result = await api.usersGET(search, limit, "cilent");

    if (result.request.status == 200) {
      setLoading(false);
      if (result.data.total < limit) {
        setLoadBtnShow(false);
      } else {
        setLoadBtnShow(true);
      }
      setUser(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const profile = async (id) => {
    dispatch({ type: "loaderModal" });
    const result = await api.profileGET(id);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      dispatch({
        type: "viewProfileModal",
        payload: { value: true, obj: result.data.details },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  return (
    <div>
      <CCard style={{ marginTop: "10px" }}>
        <CCardBody>
          <CForm>
            <div className="d-flex">
              <CCol>
                <CInputGroup size="sm">
                  <CFormInput
                    type="search"
                    placeholder="search name . . ."
                    value={search}
                    onChange={(e) => {
                      setSearch(e.target.value);
                    }}
                  />
                  <CInputGroupText>
                    <CIcon icon={cilSearch}></CIcon>
                  </CInputGroupText>
                </CInputGroup>
              </CCol>
              <CCol md={8} className="d-flex p-0 justify-content-end px-2 ">
                {/* <CCol className="d-flex pb-2 justify-content-end mt-2"> */}

                <CButton
                  color="primary"
                  size="sm"
                  onClick={() =>
                    dispatch({ type: "addProfileModal", payload: true })
                  }
                  disabled={disabled}
                >
                  Add User
                </CButton>
              </CCol>
            </div>
          </CForm>
        </CCardBody>
      </CCard>
      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody style={{ overflowX: "scroll", minHeight: "28rem" }}>
          <CHeaderDivider />
          <CTable hover small>
            <CTableHead>
              <CTableRow>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {users.map((val, id) => {
                return (
                  <CTableRow key={id} style={{ fontSize: "0.8rem" }}>
                    <CTableHeaderCell scope="row">{id + 1}</CTableHeaderCell>
                    <CTableDataCell>
                      <img
                        src={val.filepath ? val.filepath : Profile}
                        style={{ width: "35px" }}
                      />
                    </CTableDataCell>
                    <CTableDataCell
                      style={{
                        whiteSpace: "nowrap",
                      }}
                    >
                      {val.name}
                    </CTableDataCell>
                    <CTableDataCell>{val.email}</CTableDataCell>

                    <CTableDataCell>
                      <CButton
                        shape="rounded-pill"
                        color="info"
                        className="d-flex justify-content-center text-white align-items-center px-3"
                        style={{
                          fontSize: "0.75rem",
                        }}
                        size="sm"
                        onClick={() => profile(val._id)}
                      >
                        <CIcon icon={cilUser} size="sm" />
                        <span className="px-1 pr-0 p-0">Profile </span>
                      </CButton>
                    </CTableDataCell>
                    <CTableDataCell>
                      {val.status == "passout" ? (
                        <CIcon
                          icon={cilBadge}
                          size="xl"
                          style={{ marginLeft: "30px" }}
                        />
                      ) : val.status == "active" ? (
                        <CButton
                          color="success"
                          className="d-flex justify-content-center text-white align-items-center px-4 "
                          size="sm"
                          shape="rounded-pill"
                          style={{
                            fontSize: "0.75rem",
                          }}
                        >
                          Active
                        </CButton>
                      ) : (
                        <CButton
                          color="secondary"
                          size="sm"
                          shape="rounded-pill"
                          className="d-flex justify-content-center text-white align-items-center px-3"
                          style={{
                            fontSize: "0.75rem",
                          }}
                        >
                          Disabled
                        </CButton>
                      )}
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
              position: "sticky",
              left: "0px",
            }}
          >
            {loadbtnShow ? (
              loading ? (
                <div className="spinner-border text-info"></div>
              ) : (
                <CButton
                  color="secondary"
                  variant="ghost"
                  onClick={() => {
                    setLimit(limit + limit);
                  }}
                >
                  Load more
                </CButton>
              )
            ) : null}

            {users.length == 0 && loadbtnShow == false ? (
              <div>No Records</div>
            ) : null}
          </div>
        </CCardBody>
      </CCard>
    </div>
  );
};
export default Users;
