import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCol,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CNavLink,
} from "@coreui/react";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";
import CIcon from "@coreui/icons-react";
import adminroute from "../route/admin";
import admin2route from "../route/admin2";
import cilentroute from "../route/cilent";
import { useNavigate } from "react-router";
import api from "../api";
import { toast } from "react-toastify";
import Logo from "../assets/logo.png";

const AppSidebar = (props) => {
  const path = location.href;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { initialState, details, modal } = useSelector((state) => state);
  const { sidebarShow } = initialState;
  const userType = localStorage.getItem("usertype");
  const adminType = localStorage.getItem("admintype");
  const active = {
    /* background: "#58d8b1", */
    color: "black",
    cursor: "pointer",
    borderRight: "10px solid #58d8b1",
  };
  const notactive = {
    color: "black",
    cursor: "pointer",
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      navigate("/login");
    }
    myprofileGET();
  }, [path]);

  useEffect(() => {
    myprofileGET();
  }, [modal.editProfileModal]);

  const myprofileGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.myprofileGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      dispatch({
        type: "details",
        payload: result.data.details,
      });
    } else {
      console.log(result.response);
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const createMenus = (routes) => {
    return (
      <>
        {routes.map((route, id) => {
          if (route.notAppsidebar) {
            return null;
          }
          return (
            <div key={id}>
              <SimpleBar className=" my-3 ">
                {/*   <NavLink to={route.path} style={{ textDecoration: "none" }}> */}
                <CNavLink
                  style={props.name == route.name ? active : notactive}
                  className="cursor-pointer"
                  onClick={() => {
                    navigate(route.path);
                  }}
                >
                  <i className="nav-icon"></i>
                  <CIcon icon={route.icon} size="lg" />
                  <div style={{ marginLeft: "20px" }}>
                    {route.name.toUpperCase()}
                  </div>
                </CNavLink>
                {/*   </NavLink> */}
              </SimpleBar>
            </div>
          );
        })}
      </>
    );
  };
  return (
    <>
      <CSidebar position="fixed" className="mainColor" visible={sidebarShow}>
        <CSidebarNav>
          {/* User Profile Menu */}
          <CSidebarBrand className=" primary mb-3 bg-transparent" to="/">
            <CCol
              xs={12}
              className="d-flex justify-content-center align-items-center"
            >
              <img
                src={Logo}
                style={{ width: "80px", margin: "30px auto 20px auto" }}
              />
            </CCol>
          </CSidebarBrand>
          {/* All Menus  */}
          {userType == "admin"
            ? adminType == "admin2"
              ? createMenus(admin2route)
              : createMenus(adminroute)
            : createMenus(cilentroute)}
        </CSidebarNav>
        <div
          className="mb-4 px-2 d-flex justify-content-center"
          style={{ color: "black" }}
        >
          © Copyright
        </div>
      </CSidebar>
    </>
  );
};

export default AppSidebar;
