import Dashboard from "../screen/dashboard/admin";
import Users from "../screen/users/admin1";
import Notification from "../screen/notification/admin";
import Certificate from "../screen/certificate/admin";
import Status from "../screen/status/admin";
import StatusParticular from "../screen/status/admin(particular-status)";
import {
  cilHome,
  cilUser,
  cilBell,
  cilEducation,
  cilEnvelopeClosed,
} from "@coreui/icons";

const adminroute = [
  {
    path: "/dashboard",
    name: "dashboard",
    element: Dashboard,
    icon: cilHome,
  },
  {
    path: "/status",
    name: "status",
    element: Status,
    icon: cilBell,
  },
  {
    path: "/status/:id",
    name: "status",
    element: StatusParticular,
    icon: cilBell,
    notAppsidebar: true,
  },
  {
    path: "/users",
    name: "users",
    element: Users,
    icon: cilUser,
  },
  {
    path: "/notification",
    name: "notification",
    element: Notification,
    icon: cilEnvelopeClosed,
  },
  {
    path: "/certificate",
    name: "certificate",
    element: Certificate,
    icon: cilEducation,
  },
];

export default adminroute;
