import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CForm,
  CCol,
  CButton,
  CFormInput,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormTextarea,
} from "@coreui/react";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import "./sendnotification.css";
import variables from "../../variables";

const sendnotification = () => {
  const dispatch = useDispatch();
  const usertype = localStorage.getItem("usertype");
  const { modal, details } = useSelector((state) => state);
  const { sendNotificationModal } = modal;

  const [receiveId, SetReceriveId] = useState("");
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");

  const [thumbnailName, setThumbnailName] = useState("");
  const [thumbnail, setThumbnail] = useState();

  const [listVisible, setListVisible] = useState(false);
  const [searchListData, setSearchListData] = useState([]);

  useEffect(() => {
    SearchList();
  }, [receiveId]);

  useEffect(() => {
    setTimeout(() => {
      SetReceriveId(sendNotificationModal.email);
    }, 500);
  }, [sendNotificationModal.value]);

  const reset = () => {
    setThumbnail();
    setThumbnailName("");
    SetReceriveId("");
    setTitle("");
    setText("");
  };

  const notificationsend = async () => {
    const userid = localStorage.getItem("id");
    const usertoken = localStorage.getItem("token");

    if (details.email == receiveId) {
      toast.error("Enter Valid Email ID", "error");
    } else {
      const body = {
        userid,
        usertype,
        usertoken,
        receiveId: usertype == "admin" ? receiveId : variables.ADMIN_MAIL,
        title,
        text,
        files: thumbnail,
      };

      dispatch({ type: "loaderModal" });
      const response = await api.notificationsend(body);
      dispatch({ type: "loaderModal" });

      if (response) {
        if (response.request.status == 200) {
          reset();
          toast.success("Notification send successfully . . . 🎉");
          dispatch({ type: "sendNotificationModal", payload: false });
        } else {
          if (response.response.data.err.message == "login session expire") {
            dispatch({ type: "addProfileModal", payload: false });
            localStorage.clear();
            toast.error("login session expire", "error");
            navigate("/login");
          } else {
            toast.error(response.response.data.err.message, "error");
          }
        }
      }
    }
  };

  const SearchList = async () => {
    const result = await api.usersGET(receiveId, 5);

    if (result.request.status == 200) {
      setSearchListData(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      }
    }
  };

  const close = () => {
    setThumbnail();
    setThumbnailName("");
    dispatch({
      type: "sendNotificationModal",
      payload: { value: false },
    });
  };
  return (
    <>
      <CModal visible={sendNotificationModal.value} onClose={() => close()}>
        <CModalHeader onClose={() => close()}>
          <CModalTitle>Send Notification</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="row g-3">
            {usertype == "admin" ? (
              <CCol md={12}>
                <div className="search-parent">
                  <CFormInput
                    type="name"
                    value={receiveId}
                    onChange={(e) => {
                      SetReceriveId(e.target.value);
                    }}
                    label="To"
                    placeholder="Email Id . . . "
                    onFocus={() => {
                      setListVisible(true);
                    }}
                    onBlur={() => {
                      setTimeout(() => {
                        setListVisible(false);
                      }, 500);
                    }}
                  />

                  {listVisible ? (
                    <div className="search-list">
                      {searchListData.map((val, id) => {
                        if (val.userType == "admin") {
                          return null;
                        }
                        return (
                          <div
                            key={id}
                            className="search-contain"
                            onClick={() => {
                              SetReceriveId(val.email);
                              setListVisible(false);
                            }}
                          >
                            {val.email}
                          </div>
                        );
                      })}
                    </div>
                  ) : null}
                </div>
              </CCol>
            ) : null}

            <CCol md={8}>
              <CFormInput
                type="title"
                value={title}
                onChange={(e) => {
                  setTitle(e.target.value);
                }}
                id="floatingInput"
                label="Title (optional)"
              />
            </CCol>
            <CCol md={12}>
              <CFormTextarea
                label="Message"
                value={text}
                onChange={(e) => {
                  setText(e.target.value);
                }}
                rows="3"
              ></CFormTextarea>
            </CCol>

            <CCol md={8}>
              <CFormInput
                type="file"
                value={thumbnailName}
                multiple
                onChange={(e) => {
                  setThumbnail(e.target.files);
                  setThumbnailName(e.target.value);
                }}
                label="Files (optional)"
              />
            </CCol>
            {thumbnailName ? (
              <CCol md={1}>
                <i
                  className="fa fa-trash text-danger fa-2x py-1"
                  onClick={() => {
                    setThumbnail();
                    setThumbnailName("");
                  }}
                ></i>
              </CCol>
            ) : null}
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="secondary"
            onClick={() => {
              reset();
            }}
          >
            Reset
          </CButton>
          <CButton
            color="primary"
            onClick={() => {
              notificationsend();
            }}
          >
            Send Notification
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default sendnotification;
