import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";

const myprofileView = () => {
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const { myprofileViewModal } = modal;

  const editfunction = () => {
    dispatch({
      type: "myprofileViewModal",
      payload: { value: false },
    });

    dispatch({
      type: "editProfileModal",
      payload: true,
    });
  };

  return (
    <>
      <CModal
        size="xl"
        scrollable
        visible={myprofileViewModal.value}
        onClose={() =>
          dispatch({ type: "myprofileViewModal", payload: { value: false } })
        }
      >
        <CModalHeader
          onClose={() =>
            dispatch({ type: "myprofileViewModal", payload: { value: false } })
          }
        >
          <CModalTitle>Profile Details</CModalTitle>
        </CModalHeader>

        <CModalBody>
          <CRow>
            <CCol
              md={4}
              style={{
                marginTop: "10px",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <img
                src={details.filepath == "---" ? Profile : details.filepath}
                style={{
                  marginTop: "7px",
                  width: "22rem",
                  height: "22rem",
                }}
                alt="profile_picture"
              />
            </CCol>

            <CCol md={8} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>Name</CTableHeaderCell>
                        <CTableDataCell>{details.name}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Email</CTableHeaderCell>
                        <CTableDataCell>{details.email}</CTableDataCell>
                      </CTableRow>

                      <CTableRow>
                        <CTableHeaderCell> mobile</CTableHeaderCell>
                        <CTableDataCell>{details.mobile}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>birth date</CTableHeaderCell>
                        <CTableDataCell>{details.dob}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>gender</CTableHeaderCell>
                        <CTableDataCell>{details.gender}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>state</CTableHeaderCell>
                        <CTableDataCell>{details.state}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>district</CTableHeaderCell>
                        <CTableDataCell>{details.district}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Course</CTableHeaderCell>
                        <CTableDataCell>{details.course}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Year</CTableHeaderCell>
                        <CTableDataCell>{details.year}</CTableDataCell>
                      </CTableRow>
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>
            <CCol md={5} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>
                          <i
                            className="fa-brands fa-github"
                            style={{ marginRight: "7px" }}
                          />
                          GitHub
                        </CTableHeaderCell>
                        <CTableDataCell>{details.github}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>
                          <i
                            className="fa-brands fa-linkedin"
                            style={{ marginRight: "7px" }}
                          />
                          LinkedIn
                        </CTableHeaderCell>
                        <CTableDataCell>{details.linkedin}</CTableDataCell>
                      </CTableRow>
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>

            <CCol md={5} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>Joining Date</CTableHeaderCell>
                        <CTableDataCell>{details.joinDate}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Duration</CTableHeaderCell>
                        <CTableDataCell>{details.duration}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Status</CTableHeaderCell>
                        <CTableDataCell>{details.status}</CTableDataCell>
                      </CTableRow>
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </CModalBody>
        <CModalFooter>
          <CButton color="info" onClick={() => editfunction()}>
            Edit
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default myprofileView;
