import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { CModal, CModalBody } from "@coreui/react";
import "./loader.css";

const loader = () => {
  const { modal } = useSelector((state) => state);
  const { loaderModal } = modal;

  return (
    <>
      <CModal
        visible={loaderModal}
        size="sm"
        alignment="center"
        className="backblur p-0"
      >
        <div className="mainL">
          <div className="loadersL">
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </CModal>
    </>
  );
};

export default loader;
