import Dashboard from "../screen/dashboard/admin2";
import Users from "../screen/users/admin2";
import Status from "../screen/status/admin";
import { cilHome, cilUser, cilBell } from "@coreui/icons";
import StatusParticular from "../screen/status/admin(particular-status)";

const admin2route = [
  {
    path: "/dashboard",
    name: "dashboard",
    element: Dashboard,
    icon: cilHome,
  },
  {
    path: "/status",
    name: "status",
    element: Status,
    icon: cilBell,
  },
  {
    path: "/status/:id",
    name: "status",
    element: StatusParticular,
    icon: cilBell,
    notAppsidebar: true,
  },
  {
    path: "/users",
    name: "users",
    element: Users,
    icon: cilUser,
  },
];

export default admin2route;
