import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CDatePicker,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CFormSelect,
  CFormInput,
  CButton,
  CRow,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
} from "@coreui/react";
import { DateRangePicker } from "rsuite";
import Profile from "../../assets/profile.jpg";
import { toast } from "react-toastify";
import { useNavigate } from "react-router";
import api from "../../api";

const editProfile = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const { editProfileModal } = modal;

  const [thumbnailName, setThumbnailName] = useState("");
  const [thumbnail, setThumbnail] = useState();
  const [thumbnailPreview, setThumbnailPreview] = useState();

  const [name, setName] = useState("");
  const [mobile, setMobile] = useState("");
  const [dob, setDob] = useState("");
  const [course, setCourse] = useState(false);
  const [state, setState] = useState("");
  const [district, setDistrict] = useState("");
  const [year, setYear] = useState(false);
  const [gender, setGender] = useState("");
  const [github, setGithub] = useState("");
  const [linkedin, setLinkedin] = useState("");

  const changesValues = () => {
    setName(details.name != "---" ? details.name : "");
    setMobile(details.mobile != "---" ? details.mobile : "");
    setDob(details.dob != "---" ? details.dob : "");
    setCourse(details.course != "---" ? details.course : "");
    setState(details.state != "---" ? details.state : "");
    setDistrict(details.district != "---" ? details.district : "");
    setYear(details.year != "---" ? details.year : "");
    setGender(details.gender != "---" ? details.gender : "");
    setGithub(details.github != "---" ? details.github : "");
    setLinkedin(details.linkedin != "---" ? details.linkedin : "");
  };

  useEffect(() => {
    changesValues();
  }, [details.email]);

  const profileupdate = async () => {
    const userid = localStorage.getItem("id");
    const usertoken = localStorage.getItem("token");
    const usertype = localStorage.getItem("usertype");

    const body = {
      userid,
      usertype,
      usertoken,
      name: name.trim(),
      mobile,
      gender,
      dob,
      state,
      district,
      course,
      year,
      github,
      linkedin,
      image: thumbnail,
      thumbnailName: thumbnailName == "" ? false : true,
    };
    dispatch({ type: "loaderModal" });
    const result = await api.profileupdate(body);
    dispatch({ type: "loaderModal" });

    console.log(result.response);
    if (result.request.status == 200) {
      dispatch({ type: "editProfileModal", payload: false });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const close = () => {
    setThumbnail();
    setThumbnailName("");
    setThumbnailPreview();
    dispatch({ type: "editProfileModal", payload: false });
  };

  return (
    <>
      <CModal scrollable visible={editProfileModal} onClose={() => close()}>
        <CModalHeader onClose={() => close()}>
          <CModalTitle>Profile Details</CModalTitle>
        </CModalHeader>

        <CModalBody>
          <CRow className="justify-content-center pb-4">
            {thumbnailPreview ? (
              <img
                src={thumbnailPreview}
                style={{ width: "200px" }}
                alt="thumbnail"
              />
            ) : (
              <img
                src={details.filepath == "---" ? Profile : details.filepath}
                style={{ width: "400px" }}
                alt="thumbnail"
              />
            )}
          </CRow>

          <CRow>
            <CCol md={10}>
              <CFormInput
                type="file"
                value={thumbnailName}
                accept=".jpg"
                onChange={(e) => {
                  setThumbnail(e.target.files?.[0]);
                  setThumbnailName(e.target.value);
                  setThumbnailPreview(URL.createObjectURL(e.target.files?.[0]));
                }}
                label="file size max : 1 MB"
              />
            </CCol>
            {thumbnailPreview ? (
              <CCol md={1}>
                <i
                  className="fa fa-trash text-danger fa-2x py-1"
                  onClick={() => {
                    setThumbnail();
                    setThumbnailName("");
                    setThumbnailPreview();
                  }}
                ></i>
              </CCol>
            ) : null}
          </CRow>
          <CRow className=" py-3">
            <CCol md={12}>
              <CFormInput
                type="name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                label="Name"
              />
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="number"
                value={mobile}
                onChange={(e) => {
                  setMobile(e.target.value);
                }}
                label="Mobile"
              />
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="date"
                label="Date of Birth"
                value={dob}
                onChange={(e) => {
                  console.log(e.target.value);
                  setDob(e.target.value);
                }}
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="text"
                value={district}
                onChange={(e) => {
                  setDistrict(e.target.value);
                }}
                label="District"
              />
            </CCol>

            <CCol md={12}>
              <CFormInput
                type="text"
                value={state}
                onChange={(e) => {
                  setState(e.target.value);
                }}
                label="State"
              />
            </CCol>

            <CCol md={12}>
              <CFormSelect
                value={course}
                onChange={(e) => {
                  setCourse(e.target.value);
                }}
                label="Course"
              >
                <option value={false}>Select Course</option>
                <option value="bsc">Bsc</option>
                <option value="msc">Msc</option>
                <option value="bTech">B.tech</option>
                <option value="mTech">M.tech</option>
                <option value="bca">BCA</option>
                <option value="mca">MCA</option>
                <option value="other">Other</option>
              </CFormSelect>
            </CCol>

            <CCol md={12}>
              <CFormSelect
                value={year}
                onChange={(e) => {
                  setYear(e.target.value);
                }}
                label="Year"
              >
                <option value={false}>Select Year</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </CFormSelect>
            </CCol>
            <CCol md={12}>
              <CFormSelect
                value={gender}
                onChange={(e) => {
                  setGender(e.target.value);
                }}
                label="Gender"
              >
                <option value={false}>Select Gender</option>
                <option value="male">Male</option>
                <option value="female">Female</option>
              </CFormSelect>
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="text"
                value={github}
                onChange={(e) => {
                  setGithub(e.target.value);
                }}
                label="GitHub"
              />
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="text"
                value={linkedin}
                onChange={(e) => {
                  setLinkedin(e.target.value);
                }}
                label="LinkedIn"
              />
            </CCol>
          </CRow>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="success"
            onClick={() => {
              profileupdate();
            }}
          >
            Save
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default editProfile;
