import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CForm,
  CFormSelect,
  CCol,
  CButton,
  CFormInput,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
} from "@coreui/react";
import api from "../../api";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router";

const adduser = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { addProfileModal } = modal;

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [duration, setDuration] = useState("1 year");

  const reset = () => {
    setName("");
    setEmail("");
    setDuration("1 year");
  };

  const addUser = async () => {
    const body = { name, email, duration };

    dispatch({ type: "loaderModal" });
    const response = await api.useradd(body);
    dispatch({ type: "loaderModal" });

    if (response) {
      if (response.request.status == 200) {
        reset();
        toast.success(response.data.message, "success");
        dispatch({ type: "addProfileModal", payload: false });
      } else {
        if (response.response.data.err.message == "login session expire") {
          dispatch({ type: "addProfileModal", payload: false });
          localStorage.clear();
          toast.error("login session expire", "error");
          navigate("/login");
        } else {
          toast.error(response.response.data.err.message, "error");
        }
      }
    }
  };

  return (
    <>
      <CModal
        visible={addProfileModal}
        size="sm"
        alignment="center"
        onClose={() => dispatch({ type: "addProfileModal", payload: false })}
      >
        <CModalHeader
          onClose={() => dispatch({ type: "addProfileModal", payload: false })}
        >
          <CModalTitle>Register Intern</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="row g-3">
            <CCol md={12}>
              <CFormInput
                type="name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                label="Name"
              />
            </CCol>
            <CCol md={12}>
              <CFormInput
                type="email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                label="Email"
              />
            </CCol>
            <CCol md={6}>
              <CFormSelect
                label="Select Duration"
                value={duration}
                onChange={(e) => {
                  setDuration(e.target.value);
                }}
              >
                <option value={"1year"}>1 year</option>
                <option value={"6month"}>6 month</option>
              </CFormSelect>
            </CCol>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="secondary"
            onClick={() => {
              reset();
            }}
          >
            Reset
          </CButton>
          <CButton
            color="primary"
            onClick={() => {
              addUser();
            }}
          >
            Send Notification
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default adduser;
