import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CHeaderDivider,
  CTable,
  CTableHead,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CForm,
  CRow,
  CFormSelect,
} from "@coreui/react";
import { cilBadge } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import { cilSearch } from "@coreui/icons";
import Profile from "../../assets/profile.jpg";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import { cilUser, cilSend, cilTrash } from "@coreui/icons";

const Users = () => {
  const adminType = localStorage.getItem("admintype");
  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { addProfileModal, addcertificateModal, viewProfileModal } = modal;
  const navigate = useNavigate();

  const [search, setSearch] = useState("");
  const [limit, setLimit] = useState(25);
  const [loading, setLoading] = useState(false);
  const [loadbtnShow, setLoadBtnShow] = useState(true);
  const [users, setUser] = useState([]);
  const [searchType, setSearchType] = useState("cilent");

  useEffect(() => {
    usersGET();
  }, [
    addProfileModal,
    search,
    limit,
    addcertificateModal.value,
    searchType,
    viewProfileModal.value,
  ]);

  useEffect(() => {
    setTimeout(() => {
      setLimit(10);
    }, 50);
  }, [search]);

  const usersGET = async () => {
    setLoading(true);
    const result = await api.usersGET(search, limit, searchType);

    if (result.request.status == 200) {
      setLoading(false);
      if (result.data.total < limit) {
        setLoadBtnShow(false);
      } else {
        setLoadBtnShow(true);
      }
      setUser(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const profile = async (id) => {
    dispatch({ type: "loaderModal" });
    const result = await api.profileGET(id);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      dispatch({
        type: "viewProfileModal",
        payload: { value: true, obj: result.data.details },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  return (
    <div>
      <CCard style={{ marginTop: "10px" }}>
        <CCardBody>
          <CForm>
            <CRow>
              <CCol md={4} className="mb-2">
                <CInputGroup size="sm">
                  <CFormInput
                    type="search"
                    placeholder="search name . . ."
                    value={search}
                    onChange={(e) => {
                      setSearch(e.target.value);
                    }}
                  />
                  <CInputGroupText>
                    <CIcon icon={cilSearch}></CIcon>
                  </CInputGroupText>
                </CInputGroup>
              </CCol>
            </CRow>
          </CForm>
        </CCardBody>
      </CCard>
      <CCard style={{ marginTop: "10px", marginBottom: "10px" }}>
        <CCardBody
          style={{
            overflowX: "scroll",
            minHeight: "70vh",
            position: "relative",
          }}
        >
          <div
            className="d-flex mb-3"
            style={{ position: "sticky", left: "0px" }}
          >
            <CCol>
              <CFormSelect
                size="sm"
                className="p-1 px-2"
                style={{ width: "120px" }}
                value={searchType}
                onChange={(e) => {
                  setSearchType(e.target.value);
                }}
                disabled={search ? true : false}
              >
                {search ? (
                  <option>All</option>
                ) : (
                  <>
                    <option value={"cilent"}>Cilent</option>
                    <option value={"admin"}>Admin</option>
                  </>
                )}
              </CFormSelect>
            </CCol>

            <CCol md={9} className="d-flex p-0 justify-content-end px-2 ">
              <CButton
                color="primary"
                size="sm"
                className="py-0 px-2 "
                onClick={() =>
                  dispatch({ type: "addProfileModal", payload: true })
                }
                style={{ fontSize: "0.75rem" }}
              >
                Add User
              </CButton>
            </CCol>
          </div>
          <CHeaderDivider />
          <CTable hover small>
            <CTableHead>
              <CTableRow style={{ fontWeight: "500", color: "black" }}>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
                <CTableHeaderCell scope="col"></CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {users.map((val, id) => {
                return (
                  <CTableRow
                    key={id}
                    style={{ fontSize: "0.8rem", whiteSpace: "nowrap" }}
                  >
                    <CTableHeaderCell scope="row">{id + 1}</CTableHeaderCell>
                    <CTableDataCell>
                      <div style={{ position: "relative" }}>
                        <img
                          src={val.filepath ? val.filepath : Profile}
                          style={{ width: "35px" }}
                        />
                      </div>
                    </CTableDataCell>
                    <CTableDataCell>
                      <span> {val.name}</span>
                      {val.userType == "admin" ? (
                        <i
                          className="fa fa-circle-check text-info "
                          style={{ marginLeft: "7px" }}
                        ></i>
                      ) : null}
                    </CTableDataCell>
                    <CTableDataCell>{val.email}</CTableDataCell>

                    <CTableDataCell>
                      {val.userType == "cilent" ? (
                        <CButton
                          size="sm"
                          shape="rounded-pill"
                          className="d-flex justify-content-center text-white align-items-center px-3"
                          onClick={() =>
                            dispatch({
                              type: "sendNotificationModal",
                              payload: { value: true, email: val.email },
                            })
                          }
                          style={{
                            fontSize: "0.75rem",
                            backgroundColor: "#0c2d83",
                          }}
                        >
                          <CIcon icon={cilSend} size="sm" />
                          <span className="px-1 pr-0 p-0">Send </span>
                        </CButton>
                      ) : null}
                    </CTableDataCell>
                    <CTableDataCell>
                      <CButton
                        color="info"
                        size="sm"
                        onClick={() => profile(val._id)}
                        shape="rounded-pill"
                        className="d-flex justify-content-center text-white align-items-center px-3"
                        style={{
                          fontSize: "0.75rem",
                        }}
                      >
                        <CIcon icon={cilUser} size="sm" />
                        <span className="px-1 pr-0 p-0">Profile </span>
                      </CButton>
                    </CTableDataCell>
                    <CTableDataCell>
                      {val.status == "passout" ? (
                        <CIcon
                          icon={cilBadge}
                          size="xl"
                          style={{ marginLeft: "30px" }}
                        />
                      ) : val.status == "active" ? (
                        <CButton
                          color="success"
                          className="d-flex justify-content-center text-white align-items-center px-4 "
                          size="sm"
                          shape="rounded-pill"
                          style={{
                            fontSize: "0.75rem",
                          }}
                        >
                          Active
                        </CButton>
                      ) : (
                        <CButton
                          color="secondary"
                          size="sm"
                          shape="rounded-pill"
                          className="d-flex justify-content-center text-white align-items-center px-3"
                          style={{
                            fontSize: "0.75rem",
                          }}
                        >
                          Disabled
                        </CButton>
                      )}
                    </CTableDataCell>
                  </CTableRow>
                );
              })}
            </CTableBody>
          </CTable>
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "center",
              position: "sticky",
              left: "0px",
            }}
          >
            {loadbtnShow ? (
              loading ? (
                <div className="spinner-border text-info"></div>
              ) : (
                <CButton
                  color="secondary"
                  variant="ghost"
                  onClick={() => {
                    setLimit(limit + limit);
                  }}
                >
                  Load more
                </CButton>
              )
            ) : null}

            {users.length == 0 && loadbtnShow == false ? (
              <div>No Records</div>
            ) : null}
          </div>
        </CCardBody>
      </CCard>
    </div>
  );
};
export default Users;
