const profileupdate = async (body) => {
  try {
    let formData = new FormData();
    Object.keys(body).forEach((item) => {
      if (body?.[item]) {
        if (body?.[item]?.name) {
          formData.append(item, body?.[item]);
        } else if (
          (isArray(body?.[item]) || isObject(body?.[item])) &&
          !isDate(body?.[item])
        ) {
          formData.append(item, JSON.stringify(body?.[item]));
        } else {
          formData.append(item, body?.[item]?.toString());
        }
      }
    });

    const response = await axiosInstance.patch("/profile", formData, {
      headers: {
        usertoken: localStorage.getItem("token"),
        userid: localStorage.getItem("id"),
        usertype: localStorage.getItem("usertype"),
        admintype: localStorage.getItem("admintype"),
      },
    });
    return response;
  } catch (e) {
    return e;
  }
};

const notificationsend = async (body) => {
  try {
    let formData = new FormData();
    Object.keys(body).forEach((item) => {
      if (body?.[item]) {
        if (body?.[item]?.name) {
          formData.append(item, body?.[item]);
        } else if (
          isArray(body?.[item]) ||
          (isObject(body?.[item]) && !isDate(body?.[item]))
        ) {
          let i = 0;
          Array.from(body?.[item]).forEach((file) => {
            formData.append(body?.[item][i], file);
            i++;
          });
        } else {
          formData.append(item, body?.[item]?.toString());
        }
      }
    });

    const response = await axiosInstance.post("/notification", formData, {
      headers: {
        usertoken: localStorage.getItem("token"),
        userid: localStorage.getItem("id"),
        usertype: localStorage.getItem("usertype"),
        admintype: localStorage.getItem("admintype"),
      },
    });
    return response;
  } catch (e) {
    return e;
  }
};

