import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CTableHead,
  CCardTitle,
  CCardSubtitle,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";
import { useNavigate } from "react-router";
import api from "../../api";
import { toast } from "react-toastify";
import formatDate from "../../methods/formatDate";
import "./style.css";

const Dashboard = () => {
  const adminType = localStorage.getItem("admintype");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [records, setRecords] = useState([]);
  const [notificationList, setNotificatinList] = useState([]);
  const [users, setUser] = useState([]);

  useEffect(() => {
    dashboardGET();
    notificationGET();
    usersGET();
  }, []);

  const dashboardGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.dashboardGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setRecords(result.data.records);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const notificationGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.notificationGET(null, 10);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setNotificatinList(result.data.notifications);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const usersGET = async () => {
    dispatch({ type: "loaderModal" });
    const result = await api.statusUserListGET();
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      setUser(result.data.Users);
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const notification_modal_view = async (val) => {
    const userid = localStorage.getItem("id");
    const body = {  textid: val._id };
 
    if (userid == val.receiveId) {
      dispatch({ type: "loaderModal" });
      const response = await api.notificationchecked(body);
     
      dispatch({ type: "loaderModal" });

      if (response) {
        if (response.request.status == 200) {
          dispatch({
            type: "viewNotificationModal",
            payload: { value: true, obj: val },
          });
          notificationGET();
          dashboardGET();
        } else {
          if (response.response.data.err.message == "login session expire") {
            dispatch({ type: "addProfileModal", payload: false });
            localStorage.clear();
            toast.error("login session expire", "error");
            navigate("/login");
          } else {
            toast.error(response.response.data.err.message, "error");
          }
        }
      }
    } else {
      dispatch({
        type: "viewNotificationModal",
        payload: { value: true, obj: val },
      });
    }
  };

  const profile = async (id) => {
    dispatch({ type: "loaderModal" });
    const result = await api.profileGET(id);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      dispatch({
        type: "viewProfileModal",
        payload: { value: true, obj: result.data.details },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const filterText = (x) => {
    const res = x.length >= 10 ? x.slice(0, 10) + "..." : x;
    return res;
  };

  return (
    <>
      <div
        style={{
          display: "flex",
          scrollSnapType: "x mandatory",
          scrollPadding: "25px",
          columnGap: "20px",
          justifyContent: "space-between",
          paddingBottom: "0.5rem",
          overflowX: "scroll",
          overflowY: "hidden",
          marginTop: "25px",
          marginBottom: "15px",
        }}
        className="dashboard-scroll"
      >
        {records.map((val, id) => {
          return (
            <CCard
              style={{
                width: "200px",
                height: "125px",
                borderRadius: "0px",
                borderLeft: "3px solid #58d8b1",
                flex: "0 0 200px",
                scrollSnapAlign: "start",

                padding: "5px",
              }}
              key={id}
            >
              <CCardBody>
                <CCardTitle className="h3 ">
                  <div
                    style={{
                      width: "100%",
                      display: "flex",
                      justifyContent: "space-between",
                      alignContent: "center",
                    }}
                  >
                    <b className="py-2">{val.count}</b>
                    <i className={`${val.logo} fa-sm mt-4`}></i>
                  </div>
                </CCardTitle>
                <CCardSubtitle
                  className="mb-2 "
                  style={{
                    fontFamily: "'Poppins',sans-serif",
                    fontWeight: "bold",
                  }}
                >
                  {val.title}
                </CCardSubtitle>
              </CCardBody>
            </CCard>
          );
        })}
      </div>

      <CRow>
        <CCol md={4}>
          <CCard style={{ margin: "5px" }}>
            <CCardBody
              style={{
                overflowY: "scroll",
                height: "25rem",
                padding: "15px",
              }}
            >
              <div
                style={{
                  position: "sticky",
                  top: "-15px",
                  paddingTop: "15px",
                  paddingBottom: "20px",
                  backgroundColor: "white",
                }}
              >
                <CRow>
                  <CCol>
                    <CCol className="d-flex pb-2 justify-content-between">
                      <h4
                        style={{
                          fontFamily: "'Poppins',sans-serif",
                          fontWeight: "bold",
                          marginLeft: "5px",
                        }}
                      >
                        Users
                      </h4>
                      <CButton
                        color="primary"
                        size="sm"
                        onClick={() => {
                          navigate("/users");
                        }}
                      >
                        See All
                      </CButton>
                    </CCol>
                  </CCol>
                </CRow>
              </div>

              {/* User Table */}
              <CTable>
                <CTableHead
                  style={{
                    position: "sticky",
                    top: "35px",
                    backgroundColor: "white",
                  }}
                >
                  <CTableRow>
                    <CTableHeaderCell scope="col">Image</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col"></CTableHeaderCell>
                  </CTableRow>
                </CTableHead>

                <CTableBody>
                  {users.map((val, id) => {
                    return (
                      <CTableRow key={id}>
                        <CTableDataCell>
                          <img
                            src={val.filepath ? val.filepath : Profile}
                            style={{
                              width: "50px",
                              height: "50px",
                              borderRadius: "100px",
                            }}
                          />
                        </CTableDataCell>
                        <CTableDataCell
                          className="pt-3"
                          style={{ whiteSpace: "nowrap" }}
                        >
                          {val.name}
                        </CTableDataCell>

                        <CTableDataCell>
                          <CButton
                            color="info"
                            size="sm"
                            onClick={() => profile(val._id)}
                            style={{ whiteSpace: "nowrap" }}
                          >
                            View Profile
                          </CButton>
                        </CTableDataCell>
                      </CTableRow>
                    );
                  })}
                </CTableBody>
              </CTable>
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                {users.length == 0 ? <div>No Records</div> : null}
              </div>
            </CCardBody>
          </CCard>
        </CCol>

        <CCol md={8}>
          <CCard style={{ margin: "5px" }}>
            <CCardBody
              style={{
                overflowY: "scroll",
                height: "25rem",
                padding: "15px",
              }}
            >
              <div
                style={{
                  position: "sticky",
                  top: "-15px",
                  paddingTop: "15px",
                  paddingBottom: "20px",
                  backgroundColor: "white",
                }}
              >
                <CRow>
                  <CCol className="d-flex pb-2 justify-content-between">
                    <h4
                      style={{
                        fontFamily: "'Poppins',sans-serif",
                        fontWeight: "bold",
                        marginLeft: "5px",
                      }}
                    >
                      Notifications
                    </h4>
                    <CButton
                      size="sm"
                      onClick={() => {
                        navigate("/notification");
                      }}
                      style={{
                        fontSize: "0.75rem",
                        background: " #0c2d83",
                        color: "white",
                      }}
                    >
                      See All
                    </CButton>
                  </CCol>
                </CRow>
              </div>

              <CTable>
                <CTableHead
                  style={{
                    position: "sticky",
                    top: "35px",
                    backgroundColor: "white",
                    whiteSpace: "nowrap",
                  }}
                >
                  <CTableRow>
                    <CTableHeaderCell scope="col">No.</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Title</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Description</CTableHeaderCell>

                    <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                    <CTableHeaderCell scope="col"></CTableHeaderCell>
                  </CTableRow>
                </CTableHead>
                <CTableBody>
                  {notificationList.map((val, id) => {
                    const pending =
                      val.pending &&
                      val.receiveId == localStorage.getItem("id");
                    return (
                      <CTableRow
                        key={id}
                        onClick={() => {
                          notification_modal_view(val);
                        }}
                        className={pending ? "text-info" : ""}
                      >
                        <CTableDataCell>{id + 1}</CTableDataCell>
                        <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                          {pending ? <b>{val.name}</b> : val.name}
                        </CTableDataCell>

                        <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                          {val.title ? filterText(val.title) : "---"}
                        </CTableDataCell>
                        <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                          {val.text ? filterText(val.text) : "---"}
                        </CTableDataCell>
                        <CTableDataCell style={{ whiteSpace: "nowrap" }}>
                          {formatDate(val.date)}
                        </CTableDataCell>
                        <CTableDataCell>
                          {val.pending ? (
                            val.receiveId == localStorage.getItem("id") ? (
                              <i className="fa fa-circle pending"></i>
                            ) : null
                          ) : (
                            <i className="fa fa-check-double text-success" />
                          )}
                        </CTableDataCell>
                      </CTableRow>
                    );
                  })}
                </CTableBody>
              </CTable>
              <div
                style={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                {notificationList.length == 0 ? <div>No Records</div> : null}
              </div>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};
export default Dashboard;
