import Dashboard from "../screen/dashboard/cilent";
import Notification from "../screen/notification/cilent";
import Certificate from "../screen/certificate/cilent";
import Status from "../screen/status/cilent";
import {
  cilHome,
  cilBell,
  cilEducation,
  cilEnvelopeClosed,
} from "@coreui/icons";

const cilentroutes = [
  {
    path: "/dashboard",
    name: "dashboard",
    element: Dashboard,
    icon: cilHome,
  },
  {
    path: "/status",
    name: "status",
    element: Status,
    icon: cilBell,
  },
  {
    path: "/notification",
    name: "notification",
    element: Notification,
    icon: cilEnvelopeClosed,
  },
  {
    path: "/certificate",
    name: "certificate",
    element: Certificate,
    icon: cilEducation,
  },
];

export default cilentroutes;
