import React, { createElement, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CForm,
  CCol,
  CButton,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormLabel,
  CNavLink,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";
import api from "../../api";
import { toast } from "react-toastify";
import FileDownload from "js-file-download";
import variables from "../../variables";

const notificationView = () => {
  const port = variables.FILE_PORT;
  const userid = localStorage.getItem("id");
  const usertype = localStorage.getItem("usertype");

  const dispatch = useDispatch();
  const { modal } = useSelector((state) => state);
  const { viewNotificationModal } = modal;
  const { obj } = viewNotificationModal;

  const download = async (filename, originalname) => {
    const usertoken = localStorage.getItem("token");
    const userid = localStorage.getItem("id");

    const link = document.createElement("a");
    link.href = `${port}/download/${usertoken}/${userid}/${filename}/${originalname}`;
    document.body.appendChild(link);
    link.click();
  };

  const notificationdelete = async (textid) => {
    const body = { textid };
    if (usertype == "admin") {
      dispatch({ type: "loaderModal" });
      const response = await api.notificationdelete(body);
      dispatch({ type: "loaderModal" });

      if (response.request.status == 200) {
        dispatch({ type: "viewNotificationModal", payload: false });
      } else {
        if (response.response.data.err.message == "login session expire") {
          dispatch({ type: "addProfileModal", payload: false });
          localStorage.clear();
          toast.error("login session expire", "error");
          navigate("/login");
        } else {
          toast.error(response.response.data.err.message, "error");
        }
      }
    }
  };

  return (
    <>
      <CModal
        size="xl"
        /*  scrollable */
        visible={viewNotificationModal.value}
        onClose={() =>
          dispatch({ type: "viewNotificationModal", payload: false })
        }
      >
        <CModalHeader
          onClose={() =>
            dispatch({ type: "viewNotificationModal", payload: false })
          }
          style={{
            paddingBottom: "0px",
          }}
        >
          <CModalTitle
            style={{
              paddingBottom: "10px",
              display: "flex",
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className="d-flex justify-content-center align-items-center">
              <img
                src={obj.filepath ? obj.filepath : Profile}
                style={{
                  width: "3.5rem",
                  height: "3.2rem",
                  borderRadius: "100px",
                }}
              />
              <div
                className="h3"
                style={{ fontWeight: "bold", paddingLeft: "20px" }}
              >
                {usertype == "admin" ? (
                  obj.sendId == userid ? (
                    <>
                      <b>To </b>
                      {obj.receiveName}
                    </>
                  ) : (
                    obj.name
                  )
                ) : (
                  obj.name
                )}
              </div>
            </div>
            <div style={{ marginTop: "22px", marginRight: "10px" }}>
              {obj.date}
            </div>
          </CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="row g-3">
            <CCol md={12}>
              <CFormLabel className="h5 d-flex justify-content-between">
                <div>
                  <b>
                    Title <span style={{ paddingLeft: "5px" }}>:</span>
                  </b>
                  <span className="px-3">{obj.title}</span>
                </div>
                <div>
                  {usertype == "admin" ? (
                    <i
                      className="fa fa-trash text-danger mx-1"
                      onClick={() => {
                        notificationdelete(obj.id);
                      }}
                    ></i>
                  ) : null}
                </div>
              </CFormLabel>
            </CCol>

            <CCol md={12} /* style={{padding:"auto 5px auto 5px"}} */>
              <CFormLabel className="h5">
                <b>
                  Message <span style={{ paddingLeft: "5px" }}>:</span>
                </b>
              </CFormLabel>
            </CCol>

            <CCol md={12}>
              <span className="h5  ">
                <section>{obj.text}</section>
              </span>
            </CCol>
            <CCol md={12}>
              <section>
                {obj.notificationType == "applycertificate" ? (
                  usertype == "admin" ? (
                    <a
                      href={`/clinginterns/certificate/generate/${obj.name}/${obj.months}`}
                      target="_blank"
                      onClick={(e) => {
                        dispatch({
                          type: "viewNotificationModal",
                          payload: false,
                        });
                        dispatch({
                          type: "addcertificateModal",
                          payload: {
                            value: true,
                            obj: {
                              name: obj.name,
                              email: obj.email,
                              workIn: obj.workIn,
                              sdn: obj.sdn,
                            },
                          },
                        });
                      }}
                    >
                      Request To Certificate
                    </a>
                  ) : (
                    "You are Requested For Certificate"
                  )
                ) : null}
              </section>
            </CCol>

            {/* Doc */}
            <CCol md={12}>
              {obj.files.map((val, id) => {
                return (
                  <CButton
                    color="light"
                    shape="rounded-pill"
                    className="px-4 m-2"
                    key={id}
                    onClick={() => {
                      download(val.filename, val.originalname);
                    }}
                  >
                    {val.originalname}
                    <i
                      className="fa fa-download"
                      style={{ marginLeft: "10px" }}
                    ></i>
                  </CButton>
                );
              })}
            </CCol>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={() => {
              dispatch({ type: "viewNotificationModal", payload: false }),
                dispatch({
                  type: "sendNotificationModal",
                  payload: { value: true, email: obj.email },
                });
            }}
          >
            Reply
            <i className="fa fa-reply " style={{ paddingLeft: "5px" }}></i>
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default notificationView;
