import React, { useState, useEffect } from "react";
import { AppSidebar, AppHeader } from "../container/index";
import { Navigate, Routes, Route, useLocation } from "react-router";
import { useNavigate } from "react-router";
import modals from "../screen/modals/index";
import Page404 from "../screen/pages/Page404";
import adminroute from "../route/admin";
import admin2route from "../route/admin2";
import cilentroute from "../route/cilent";

const DefaultLayout = () => {
  const navigate = useNavigate();

  const totalRoutes = [
    "dashboard",
    "users",
    "status",
    "notification",
    "certificate",
  ];
  const { pathname } = useLocation();
  const [titleOfRoute, setTitleOfRoute] = useState("");
  const usertype = localStorage.getItem("usertype");
  const admintype = localStorage.getItem("admintype");

  useEffect(() => {
    {
      usertype == "admin"
        ? admintype == "admin2"
          ? titleOfRouteFun(admin2route)
          : titleOfRouteFun(adminroute)
        : titleOfRouteFun(cilentroute);
    }
  }, [pathname]);

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      navigate("/login");
    }
  }, []);

  const titleOfRouteFun = (arr) => {
    const res = totalRoutes.find((val) => {
      return val.includes(pathname.substring(1, 3));
    });
    setTitleOfRoute(res);
  };

  const createRoute = (route) => {
    return (
      <>
        {route.map((route, id) => {
          return (
            <Route
              key={id}
              path={route.path}
              element={<route.element />}
              name={route.name}
            />
          );
        })}
      </>
    );
  };

  return (
    <>
      <div>
        <AppSidebar name={titleOfRoute} />
        <div className="wrapper d-flex flex-column min-vh-100 bg-light">
          <AppHeader name={titleOfRoute} />
          <div className="body flex-grow-1 px-3  ">
            <Routes>
              {usertype == "admin"
                ? admintype == "admin2"
                  ? createRoute(admin2route)
                  : createRoute(adminroute)
                : createRoute(cilentroute)}
              <Route path="*" element={<Navigate to="/dashboard" />} />
            </Routes>
          </div>
        </div>
      </div>

      {modals.adduser()}
      {modals.profileView()}
      {modals.sendnotification()}
      {modals.notificationView()}
      {modals.editProfile()}
      {modals.myprofileView()}
      {modals.addcertificateModal()}
      {modals.editStatus()}
    </>
  );
};

export default DefaultLayout;
