import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCard,
  CCardBody,
  CCol,
  CTable,
  CTableRow,
  CTableHeaderCell,
  CTableDataCell,
  CTableBody,
  CButton,
  CRow,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CModalBody,
  CFormSelect,
} from "@coreui/react";
import Profile from "../../assets/profile.jpg";
import api from "../../api";
import { toast } from "react-toastify";

const profileView = () => {
  const dispatch = useDispatch();
  const { modal, details } = useSelector((state) => state);
  const { viewProfileModal } = modal;
  const { obj } = viewProfileModal;

  const [status, setStatus] = useState("active");
  const [make, setMake] = useState("cilent");
  const [disabled, setDisabled] = useState(true);

  useEffect(() => {
    setDisabled(details.status == "active" ? false : true);
  }, [details.status]);

  const saveUpdate = async () => {
    const id = obj.id;
    const body = { id, status, make };
    dispatch({ type: "loaderModal" });
    const result = await api.userupdate(body);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      toast.success(result.data.message, "successfully update");
      dispatch({
        type: "viewProfileModal",
        payload: { value: false },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  const userdelete = async (id) => {
    const body = { id };
    dispatch({ type: "loaderModal" });
    const result = await api.userdelete(body);
    dispatch({ type: "loaderModal" });

    if (result.request.status == 200) {
      toast.success(result.data.message, "successfully deleted");
      dispatch({
        type: "viewProfileModal",
        payload: { value: false },
      });
    } else {
      if (result.response.data.err.message == "login session expire") {
        localStorage.clear();
        toast.error("login session expire", "error");
        navigate("/login");
      } else {
        toast.error(result.response.data.err.message, "error");
      }
    }
  };

  return (
    <>
      <CModal
        size="xl"
        scrollable
        visible={viewProfileModal.value}
        onClose={() =>
          dispatch({ type: "viewProfileModal", payload: { value: false } })
        }
      >
        <CModalHeader
          onClose={() =>
            dispatch({ type: "viewProfileModal", payload: { value: false } })
          }
        >
          <CModalTitle>Profile Details</CModalTitle>
        </CModalHeader>

        <CModalBody>
          <CRow>
            <CCol
              md={4}
              style={{
                marginTop: "10px",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <img
                src={obj.filepath ? obj.filepath : Profile}
                style={{
                  marginTop: "7px",
                  width: "22rem",
                  height: "22rem",
                }}
              />
            </CCol>

            <CCol md={8} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>Name</CTableHeaderCell>
                        <CTableDataCell>{obj.name}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Email</CTableHeaderCell>
                        <CTableDataCell>{obj.email}</CTableDataCell>
                      </CTableRow>

                      <CTableRow>
                        <CTableHeaderCell> mobile</CTableHeaderCell>
                        <CTableDataCell>{obj.mobile}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>birth date</CTableHeaderCell>
                        <CTableDataCell>{obj.dob}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>gender</CTableHeaderCell>
                        <CTableDataCell>{obj.gender}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>state</CTableHeaderCell>
                        <CTableDataCell>{obj.state}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>district</CTableHeaderCell>
                        <CTableDataCell>{obj.district}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Course</CTableHeaderCell>
                        <CTableDataCell>{obj.course}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>Year</CTableHeaderCell>
                        <CTableDataCell>{obj.year}</CTableDataCell>
                      </CTableRow>
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>

            <CCol md={5} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>Status</CTableHeaderCell>
                        <CTableDataCell>{obj.status}</CTableDataCell>
                      </CTableRow>

                      <CTableRow>
                        <CTableHeaderCell>Duration</CTableHeaderCell>
                        <CTableDataCell>{obj.duration}</CTableDataCell>
                      </CTableRow>

                      <CTableRow>
                        <CTableHeaderCell>Joining Date</CTableHeaderCell>
                        <CTableDataCell>{obj.joinDate}</CTableDataCell>
                      </CTableRow>

                      {obj.status == "passout" ? (
                        <>
                          <CTableRow>
                            <CTableHeaderCell>End Date</CTableHeaderCell>
                            <CTableDataCell>{obj.endDate}</CTableDataCell>
                          </CTableRow>

                          <CTableRow>
                            <CTableHeaderCell>Work In</CTableHeaderCell>
                            <CTableDataCell>{obj.workIn}</CTableDataCell>
                          </CTableRow>

                          <CTableRow>
                            <CTableHeaderCell>
                              Senior Developer Name
                            </CTableHeaderCell>
                            <CTableDataCell>{obj.sdn}</CTableDataCell>
                          </CTableRow>
                        </>
                      ) : null}
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>

            <CCol md={7} style={{ marginTop: "10px" }}>
              <CCard>
                <CCardBody>
                  <CTable style={{ margin: "0px" }}>
                    <CTableBody>
                      <CTableRow>
                        <CTableHeaderCell>
                          <i
                            className="fa-brands fa-github"
                            style={{ marginRight: "7px" }}
                          />
                          GitHub
                        </CTableHeaderCell>
                        <CTableDataCell>{obj.github}</CTableDataCell>
                      </CTableRow>
                      <CTableRow>
                        <CTableHeaderCell>
                          <i
                            className="fa-brands fa-linkedin "
                            style={{ marginRight: "7px" }}
                          />
                          LinkedIn
                        </CTableHeaderCell>
                        <CTableDataCell>{obj.linkedin}</CTableDataCell>
                      </CTableRow>
                    </CTableBody>
                  </CTable>
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </CModalBody>
        <CModalFooter>
          <CFormSelect
            size="sm"
            value={status}
            onChange={(e) => {
              setStatus(e.target.value);
            }}
            style={{ width: "100px" }}
          >
            <option value={"active"}>Active</option>
            <option value={"disabled"}>Disabled</option>
            <option value={"passout"}>Passout</option>
          </CFormSelect>

          {localStorage.getItem("admintype") == "admin1" ? (
            <CFormSelect
              style={{ width: "88px" }}
              size="sm"
              value={make}
              onChange={(e) => {
                setMake(e.target.value);
              }}
            >
              <option value={"cilent"}>Cilent</option>
              <option value={"admin"}>Admin</option>
            </CFormSelect>
          ) : null}
          <CButton
            color="success"
            size="sm"
            onClick={() => {
              saveUpdate();
            }}
            className="text-white"
            disabled={disabled}
          >
            Save
          </CButton>

          <CButton
            color="danger"
            size="sm"
            className="text-white"
            onClick={() => {
              const a = confirm(
                `Are you sure you want to delete ${obj.name} user ?`
              );
              if (a) {
                userdelete(obj.id);
              }
            }}
            disabled={disabled}
          >
            Delete
          </CButton>

          {/*   {localStorage.getItem("admintype") == "admin1" ? (
            <CButton
              size="sm"
              className="text-white "
              onClick={() => {}}
              style={{ width: "315px" }}
            >
              Apply Certificate
            </CButton>
          ) : null} */}
        </CModalFooter>
      </CModal>
    </>
  );
};

export default profileView;
